
import { Table } from 'antd'

import StatusIcon from '@/Components/svg/StatusIcon'
import QiuChar from '@/Components/QiuChar'
import StatusChar from '@/Components/StatusChar'
import TypeIcon from '@/Components/TypeIcon'
import Target from '@/Components/svg/Target'
import { formatDuration } from '@/utils/seenYouUtil'
import avgheart from '@/assets/icon/avgheart.svg'
import avgRate from '@/assets/icon/avgRate.svg'

export default function hisTable({ data=[],bo=true, hideColumns = [], typeName}) {
    console.log(data)
    const columns = [
        hideColumns.includes('item') ? null :{
            title: 'item',
            dataIndex: 'reportTypeName',
            key: 'reportTypeName',
            render: (text, row) => {
                let iconSrc = row.src
                if(typeName) iconSrc = typeName === 'Heart Rate' ? avgheart : avgRate
                return (
                    <>
                        {
                            !row.color ? <>
                                <div className='flex items-center'><div className='mr-[10px]'>
                                    <div className='w-[26px] h-[26px] rounded-full bg-green flex  justify-center items-center '>
                                        {
                                            row.src ? <img src={iconSrc} className='w-full h-full' /> : <StatusIcon />
                                        }
                                    </div></div> <div className='ml-[10px] text-[16px] font-semibold'>{typeName ? (row.reportTypeName.split(' ')[0] + " " + typeName) : row.reportTypeName} </div></div>
                            </> : <>
                                <div className='flex'>
                                    <div className='w-[26px] h-[26px]' style={{ "backgroundColor": row?.color || '#18a675'}}></div>
                                    <div className='ml-[10px] text-[16px] font-semibold'>{typeName ? (row.reportTypeName.split(' ')[0] + " " + typeName) : row.reportTypeName}</div>
                                </div>
                            </>
                        }

                    </>
                )
            }
        },
        hideColumns.includes('target') ? null :{
            title: 'target',
            dataIndex: 'target',
            key: 'target',

            render: (text) => <div className='flex items-center text-[14px] '>  <Target />
                <div className='ml-[10px]'> {text}  </div></div>,

        },
        hideColumns.includes('Value') ? null :{
            title: 'Value',
            dataIndex: 'minutesCountStr',
            key: 'minutesCountStr',
            // render: (text, row) => <div className='flex items-center'>   <QiuChar status={row.priority == 0 ? 7 : row.priority == 1 ? 6 : 8} name={formatDuration(text)} /></div>,
            render: (text, row) => <div className='flex items-center'>   <QiuChar status={row.priority == 0} name={row.numberStr || row?.value || text || row.minutesCountStr} /></div>,
        },

        hideColumns.includes('trend') ? null :{
            title: 'trend',
            key: 'trend',
            dataIndex: 'trend',
            render: (text, row) => {
                if(row?.unit === 'bpm') return(<div className='flex items-center'>  <StatusChar name={Math.abs(row?.trend)+'bpm'} status={row?.trendStatu} /></div>)
                else return(<div className='flex items-center'>  <StatusChar name={formatDuration(row?.trend)} status={row?.trendStatu} /></div>)
            }
        },
        hideColumns.includes('type') ? null :{
            title: 'type',
            key: 'priority',
            dataIndex: 'priority',
            render: (text) => <TypeIcon text={text} />,
        },


    ].filter(Boolean);

    let filteredData = data
    filteredData = data.filter(item => item.reportTypeName !== "Daily Average");
    filteredData = data.filter(item => item.reportTypeName !== "avg");

    return (
        <div className={` ${bo?"min-h-[300px]":""}  bg-lightwhite`}>
            <Table
                pagination={false}
                columns={columns} dataSource={filteredData} className={data.length == 0 ? "newTable" : 'newTable1'}
                rowClassName={(record) => {
                    return `ant-F7F7FF`
                    // if (record?.priority == "1") {
                    //     return "ant-250181"
                    // } else if (record?.priority == "2") {
                    //     return `ant-2507594`
                    // } else {
                    //     return `ant-F7F7FF`
                    // }

                }}
            />
        </div>
    )
}