import React, { useEffect, useState } from 'react';
import ReactEcharts from 'echarts-for-react';
import 'echarts/lib/chart/bar';

import ic_pill from '@/assets/icon/deepsleep.svg'

function dayOfWeekFromDayNumber(dayNumber) {
    const daysOfWeek = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"];
    return daysOfWeek[dayNumber];
}

const currentDate = new Date();
let currentMonth = currentDate.getMonth() + 1;
currentMonth = currentMonth < 10 ? '0' + currentMonth : currentMonth;

function MyChart({ charData, activeTab }) {
    let dates = []
    if(activeTab === 'Month') {
        charData.xdata.forEach((item,ri)=>{
            dates[ri] = currentMonth+'-'+(item < 10 ? '0' + item : item)
        })
    } else if(activeTab === 'Week'){
        charData.xdata.forEach((item,ri)=>{
            dates[ri] = item ? dayOfWeekFromDayNumber(item-1) : ''
        })
    }
    const [option, setOption] = useState({})
    useEffect(() => {

        if (charData && charData?.xdata.length > 0) {
            MyChartinit()
        } else {

            setOption({
                title: {
                    text: "There is currently no data available",
                    show: true,
                    left: "center",
                    top: "center",
                    textStyle: {
                        color: "#000",
                        fontSize: "20px",
                        fontWeight: 400,
                    },
                },
            })
        }
    }, [charData])

    const MyChartinit = () => {
        console.log('charData.values', charData.values)
        setOption({
            backgroundColor: 'rgb(247, 247, 255)',
            grid: {
                top: "13%",
                left: "3%",
                bottom: "8%",
                right: "3%",

            },
            tooltip: {
                trigger: 'axis'
            },
            legend: {
                show: false
            },

            xAxis: {
                type: 'category',
                boundaryGap: false,
                data: dates,
                splitLine: {
                    show: false  //  x 
                },

            },

            yAxis: {
                name: 'Hours', // 
                nameLocation: 'end', // 'end' 
                nameTextStyle: { // 
                    color: '#333',
                    fontSize: 12,
                    padding: [0, 0, 10, -10] // 
                },
                splitLine: { show: false },
                axisLine: { show: true },
                type: 'value',
                axisLabel: {
                    color: "#000",
                }
            },
            color: ["#FA4B5E", "#FAB515"],
            series: [

                {
                    name: 'Lowest',
                    type: 'line',
                    data: charData.values,
                    symbol: 'none',  // 
                    smooth: true,
                    markPoint: {
                        label: {
                            show: true,
                            color: "#000",
                            position: charData.values.length >= 2 ? "left" : 'right',
                            backgroundColor: "#fff",
                            fontSize: "14px",
                            fontWeight: "bold"
                        },
                        data: [{ name: '', coord: [charData.values.length / 2, charData.average], yAxis: -1.5, value: `av.${charData.average} Hours` }],
                        // data: [
                        //     { name: '', coord: [1, 1], value: 222 }
                        // ],
                        symbolSize: [30, 30],

                        symbol: `image://${ic_pill}`
                    },
                    markLine: {
                        symbol: "none",
                        label: {
                            show: false
                        },
                        lineStyle: {
                            type: 'solid' // 
                        },
                        data: [
                            { value: 1000, name: '', yAxis: charData.average, axisLabel: { show: false } }
                        ]
                    }
                }
            ]

        })
    }





    return <div className='bg-lightwhite pt-[20px]'>
        <ReactEcharts option={option} notMerge={true}
        /></div>
}

export default MyChart;