
import React, { useEffect, useState } from 'react';
import HisCards from './assembly/HisCards'
import HisTable from './assembly/HisTable'
import SummaryBtn from '@/Components/SummaryBtn'
import ActivitiesChart from './ActivitiesChart'
const ActiviteScom = ({ cards, chartData, SetIsShow }) => {
    return (
        <>
            <div className='pt-[20px] pb-[10px] flex justify-between'>
                <div className='text-[20px] text-cblock font-semibold'>Daily Activities Report (ADL)</div>
                {SetIsShow && <SummaryBtn onClick={() => SetIsShow(true)} />}
               
            </div>
            <div className='pt-[30px] flex '>
                {
                    cards && <HisCards data={cards} />
                }
            </div>
            <div className='bg-lightwhite'>
                <ActivitiesChart data={chartData} />
                <div>
                    <HisTable data={cards} hideColumns={['target','trend','type']}/>
                    {/* <HisTable data={cards}/> */}
                </div>
            </div>
        </>
    )
}
export default ActiviteScom;