
import React, { useEffect, useRef, useState } from 'react';
import WeekCharts from './weekcharts.jsx';
import TabMenus from '@/Components/TabMenus/index'
import TimeSelection from '@/Components/TimeSelection/index'
import { sleepSummary, getReportSummary } from '@/api/detailOfElderly'
import dayjs from 'dayjs';
import './SleepScore.css';
import SleepSumLine from './SleepSumLine.jsx'
import { formatDuration } from '@/utils/seenYouUtil.js'
import HisCards from './assembly/HisCards.jsx'
import Timerline from './assembly/Timerline.jsx'
import HisTable from './assembly/HisTable.jsx'
import HistableCom from './HistableCom.jsx'
import { useCountStore } from '@/store/index';
import ModelTitle from '@/Components/ModelTitle/index'
import { Spin, Table, Tag, Modal } from 'antd';

const SleepSummary = (props) => {
    const Dailyreport = useCountStore(state => state.Dailyreport);
    const { isShow, setIsShow, calculateProportions } = props
    const [loading, setLoading] = useState(true);
    const [List, setList] = useState([
        {
            text: "Day",
            bo: true,
            warring: false
        },

        {
            text: "Week",
            bo: false,
            warring: false
        },
        {
            text: "Month",
            bo: false,
            warring: false
        }
    ]);
    const [data, setData] = useState([])
    const [activeTab, setActiveTab] = useState('Day')
    const [lines, setLines] = useState([])

    const [query, setQuery] = useState({

        "startTime": dayjs().format('YYYY-MM-DD'),
        "endTime": ""
    })
    
    const [data3, setData3] = useState([])
    


    const bioInit = async () => {
        setLoading(true)
        if (query.startTime == "") return
        setLines([])
        setData([])
        let query1 = { ...query }
        if (activeTab == "Day") {
            query1.endTime = ""
        }
        Promise.all([
            sleepSummary({
                ...query,
                ...Dailyreport
            }),
            getReportSummary()
        ])
            .then(res => {
                setData3(res[0])
                setTimeout(() => {
                    setLoading(false)
                }, 1500);
            })

    }
    const handleTimerClick = (row) => {
        setLoading(true)
        if (activeTab == "Day") {
            setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: "" })
        } else {

            setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: row?.endvalue?.format('YYYY-MM-DD') })
        }
    }

    useEffect(() => {
        if (isShow) {
            bioInit()
        } else {

            setActiveTab("Day")
        }
    }, [isShow, query])
    return (
        <div>
            {
                isShow && <div className='mode-back'><div className='sleepModel'>
                    <Spin spinning={loading} style={{marginTop: '200px'}}>
                    <ModelTitle title={"Sleep Summary"} setIsShow={setIsShow} />

                    <div className='flex flex-col items-center'>

                        <div className='flex mt-[44px]'>
                            <TabMenus menus={List} activeTab={activeTab} setActiveTab={setActiveTab} />
                        </div>
                        <div className='flex mt-[45px] w-[256px] justify-between items-center'>
                            <TimeSelection name={activeTab} handleClick={handleTimerClick} />
                        </div>
                    </div>
                    {/* <div className='pt-[65px] pb-[10px] flex justify-between'>
                        <div className='text-[20px] text-cblock font-semibold'>Sleep Summary</div>

                    </div> */}
                    <SleepSumLine setOpen={false} isShow={false} data3={data3} activeTab={activeTab} loading={loading} />

                    </Spin>
                    {/* <>
                        <div className='pt-[10px] flex '>

                            <HisCards data={data} />

                        </div>
                        <div>
                            <Timerline lines={lines} />
                        </div>

                        {
                            activeTab == "Week" && (
                                <div className='w-full h-[350px]' style={{ backgroundColor: '#F7F7FF' }}>
                                    <WeekCharts proSeries={series} />
                                </div>
                            )
                        }

                        {
                            activeTab == "Month" && (
                                <div className='w-full h-[350px]' style={{ backgroundColor: '#F7F7FF' }}>
                                    <WeekCharts proSeries={series} />
                                </div>
                            )
                        }
                        <div>
                            <HisTable data={data} />
                        </div>
                    </> */}

                    {/* <HistableCom query={query} setOpen2={false} /> */}
                </div>
                </div>
            }
        </div>
    );
};

export default SleepSummary;
