import HisTable from './assembly/HisTable.jsx'
import HisCards from './assembly/HisCards.jsx'
import Timerline from './assembly/Timerline.jsx'
import WeekCharts from './weekcharts.jsx';
import { useEffect, useState } from 'react'
import SummaryBtn from '@/Components/SummaryBtn'
import { calculateProportions } from '@/utils/comFunction.js'
import { formatDuration } from '@/utils/seenYouUtil'

export default function SleepSumLine({ activeTab, data3, setOpen, isShow, loading}) {
    const [cards, setCards] = useState([])
    const [series, setSeries] = useState([])
    const [data, setData] = useState([])
    const [lines, setLines] = useState([])
    useEffect(()=>{
        console.log("again");
    },[data3])
    function dayOfWeekFromDayNumber(dayNumber) {
        const daysOfWeek = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"];
        return daysOfWeek[dayNumber];
    }

    useEffect(() => {
        let reportDetavalusilList = []
        setData([])
        setCards([])
        setLines([])
        try {
            let res = data3

            console.log("data3",data3)
            if (!res.data){
                setSeries([])
                return
            } 
            if (activeTab == "Day" || activeTab == "Daily") {

                let groupJSON = res?.data?.groupJSON
                let reportDetailList = res?.data?.reportDetailList
                console.log("groupJSON", groupJSON)
                let jsgroupJSON = Object.values(groupJSON).flat(2)
                let data = calculateProportions(jsgroupJSON)
                setLines(data)
                reportDetavalusilList = Object.values(reportDetailList).map(e => {
                    return tableFomart(e)
                })
                setData(reportDetavalusilList)
            } else  {
                let dailyReportsWeekDTOList = activeTab === 'Week' ? res?.data?.dailyReportsWeekDTOList : res?.data?.dailyReportsMonthDTOS
                console.log("",res.data)
                let collect = res?.data?.collect
                reportDetavalusilList = Object.values(collect).map(e => {
                    return tableFomart(e)
                })
                setData(reportDetavalusilList)

                const arrayWeek = [];
                const leng = activeTab === 'Week' ? 7 : 31
                //  arrayWeek leng 
                for (let i = 0; i < leng; i++) {
                    arrayWeek.push([]);
                }
                dailyReportsWeekDTOList.forEach(item => {
                    for (let i = 0; i < leng; i++) {
                        let weekDetailValue = 0
                        if(activeTab === 'Week') weekDetailValue = item?.weekDetail[i + 1] || 0; // 0
                        else weekDetailValue = item?.monthDetail[i + 1] || 0; // 0
                        arrayWeek[i].push(weekDetailValue);
                    }
                });
                const colors = Object.values(res.data.collect).map(item => item.color);
                let seriesArr = []

                // 
                const currentDate = new Date();
                let currentMonth = currentDate.getMonth() + 1;
                currentMonth = currentMonth < 10 ? '0' + currentMonth : currentMonth;

                seriesArr = dailyReportsWeekDTOList.map((item,index) => {
                    return {
                        name: activeTab === 'Week'?dayOfWeekFromDayNumber(item.week-1):currentMonth+'-'+(item.monthday < 10 ? '0' + item.monthday : item.monthday),
                        type: 'bar',
                        stack: 'total',
                        // barWidth: '100px',
                        // label: {
                        //     show: true,
                        //     color: '#f2f2f2'
                        // },
                        itemStyle: {
                            color: colors[index]
                        },
                        data: arrayWeek[index]
                    }

                })
                setSeries(seriesArr)
            }
        } catch (error) {
            console.log("", error)
        }

        let arr = JSON.parse(JSON.stringify(reportDetavalusilList))
        arr = arr.map(e => {
            if (e.trendStatu == 1) {
                e.valueStatu = 6
            }
            if (e.trendStatu == 2) {
                e.valueStatu = 7
            }
            if (e.trendStatu == 3) {
                e.valueStatu = 8
            }
            e.bgColor = e.color
            return e
        })
        setCards(arr)
    }, [loading,data3,activeTab])
    const tableFomart = (e) => {
        if (e.reportTypeName == "out of bed") {
            e.minutesCount = e.minutesCount + "x"
        } else {
            e.minutesCount = formatDuration(e.minutesCount)
        }
        let targetValue = ""
        if (e.targetMin && e.targetMax) {
            targetValue = e.targetMin + "-" + e.targetMax
        } else if (e.targetMin) {
            targetValue = e.targetMin
        } else if (e.targetMax) {
            targetValue = e.targetMax
        }
        e.targetValue = targetValue
        if (!e.color) {
            e.color = "rgba(0,0,0,0)"
        }
        return e
    }
    return (
        <div >
                <div className='pt-[20px] pb-[10px] flex justify-between'>
                    <div className='text-[20px] text-cblock font-semibold cursor-pointer'>Sleep Summary</div>
                    {/* {isShow && <div className='text-[14px] text-cblock font-semibold h-[40px] rounded-[20px] flex items-center   pl-[20px] pr-[20px] cursor-pointer' onClick={() => setOpen(true)} style={{ 'border': ' 1px solid #333951' }} >Summary</div>} */}
                    {isShow && <SummaryBtn onClick={() => setOpen(true)} />}
                </div>
                <div className='pt-[10px]  '>
                    <>
                        <div className=' flex '>
                            <HisCards data={cards} />
                        </div>
                        
                        {
                           ( activeTab == "Day" ||activeTab == "Daily") && (
                                <div className='w-full ' style={{ backgroundColor: '#F7F7FF' }}>
                                  <Timerline lines={lines} />
                                </div>
                            )
                        }

                        {
                           ( activeTab == "Week" ||activeTab == "Weekly")    && (
                                <div className='w-full ' style={{ backgroundColor: '#F7F7FF', opacity: loading?0:1}}>
                                    <WeekCharts proSeries={series} activeTab={activeTab}/>
                                </div>
                            )
                            
                        }

                        {
                            (activeTab == "Month"||activeTab == "Monthly" ) && (
                                <div className='w-full ' style={{ backgroundColor: '#F7F7FF', opacity: loading?0:1}}>
                                    <WeekCharts proSeries={series} activeTab={activeTab}/>
                                </div>
                            )
                        }
                        <div>
                            <HisTable data={data} hideColumns={['target','trend','type']} />
                        </div>
                    </>
                </div>

        </div>
        
    )
}