
import React, { useEffect, useRef, useState } from 'react';
import TabMenus from '@/Components/TabMenus/index'
import SleepSumLine from './SleepSumLine.jsx'
import { formatDuration } from '@/utils/seenYouUtil'
import { activeTimeLine, sleepSummary, getBioVtalSummary, dailyactivity, activeTrend, listByElder, sleepScore } from '@/api/detailOfElderly'
import dayjs from 'dayjs';
import WeeklyCom from './weeklyCom.jsx'
import BotvITal from './BotvITal'
import BotvICom from './BotvICom'
import { calculateDuration } from '@/utils/comFunction'
import ActiviteScom from './ActiviteScom'
import TrendCom from './TrendCom'
import ReportCom from './reportCom.jsx'
import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';
import SleepScoreCard from './SleepScoreCard.jsx'
import HistableCom from './HistableCom.jsx'
import { useCountStore } from '@/store/index';
import { Spin, notification } from 'antd'
import weekOfYear from 'dayjs/plugin/weekOfYear';
import isoWeek from 'dayjs/plugin/isoWeek';
import debounce from '@/utils/debounce'
import TimeSelection from '@/Components/TimeSelection/index'
import ActivitiesReport from './ActivitiesReport.jsx';

dayjs.extend(weekOfYear);
dayjs.extend(isoWeek);
const Report = ({ path,Config }) => {
    let  mothObj={
        Daily:"Day",
        Weekly:"Week",
        Monthly:"Month",
        Yearly:"Year"
    }
    const Dailyreport = useCountStore(state => state.Dailyreport);
    const [activeTab, setActiveTab] = useState('Daily')
    const [data3, setData3] = useState([])
    const [card, setCard] = useState([])
    const [TrendCard, setTrendCard] = useState([])
    const [linData, setLinData] = useState([])
    const [isShow, SetIsShow] = useState(true)

    const [List, setList] = useState([

        {
            text: "Daily",
            bo: true,
            warring: false
        },

        {
            text: "Week",
            bo: false,
            warring: false
        },
        {
            text: "Month",
            bo: false,
            warring: false
        },
       
    ]);
    const [data, setData] = useState([])
    const [charData, setCharData] = useState(null)
    const [DailChartData, setDailChartData] = useState(null)
    const [DailCards, setDailCards] = useState([])
    const [query, setQuery] = useState({
        "startTime": dayjs().format('YYYY-MM-DD'),
        "endTime": dayjs().format('YYYY-MM-DD'),
        "dateType": "day"
    })
    const [cards, setCards] = useState([])
    const [loading, setLoading] = useState(false)
    const getList = () => {
        setLoading(true)
        console.log(query)
        console.log(Dailyreport)
        let body = {
            ...query, ...Dailyreport
        }
        // if (activeTab == "Daily") {
        //     body.startTime = dayjs().format('YYYY-MM-DD')
        //     body.endTime = undefined
        //     body.dateType = "day"
        // } else if (activeTab == "Weekly") {
        //     // 
        //     const startOfWeek = dayjs().startOf('isoWeek'); // ISO
        //     const endOfWeek = dayjs().endOf('isoWeek'); // ISO
        //     body.startTime = startOfWeek.format('YYYY-MM-DD')
        //     body.endTime = endOfWeek.format('YYYY-MM-DD')
        //     body.dateType = "week"
        // } else if (activeTab == "Monthly") {
        //     // 
        //     const startOfMonth = dayjs().startOf('month');
        //     // 
        //     const endOfMonth = dayjs().endOf('month');
        //     body.startTime = startOfMonth.format('YYYY-MM-DD')
        //     body.endTime = endOfMonth.format('YYYY-MM-DD')
        //     body.dateType = "month"
        // } else if (activeTab == "Yearly") {
        //     // 
        //     const startOfYear = dayjs().startOf('year');
        //     // 
        //     const endOfYear = dayjs().endOf('year');
        //     body.startTime = startOfYear.format('YYYY-MM-DD')
        //     body.endTime = endOfYear.format('YYYY-MM-DD')
        //     body.dateType = "year"
        // }
    
        sleepScore(body)
            .then(res => {
                let data=res?.data
                setCards(data)
            })

        let sleepParams = {
            ...body, ...Dailyreport
        }
        if(activeTab == "Day" || activeTab == "Daily") sleepParams.endTime = ""
        sleepSummary(sleepParams)
            .then((data3) => {

                let res3 = data3
                console.log("Res33",res3)
                setData3(res3)
                
            })
        getBioVtalSummary(sleepParams).then(res => {
            try {
                let data1 = res?.data
                setCharData(data1)
            } catch (error) {

                setCharData([])
            }
        })
        dailyactivity({
            ...body, ...Dailyreport,
            "radarType": 0
        })
            .then(res => {
                console.log("res",res.data)
                if(!res)return 
                let { detailList, groupJSON } = res.data
                let datalist = Object.values(detailList)
                datalist = datalist.map(e => {
                    return {
                        ...e,
                        minutesTimerCount: formatDuration(e.minutesCount),
                        valueStatu: e.valueStatu ? e.valueStatu : 0
                    }

                })

                // let timerdatas = calculateDuration(Object.values(groupJSON).flat(2))
                console.log(res.data)
                setDailChartData(res.data)

                setDailCards(datalist)
            })
        activeTrend({
            ...body, ...Dailyreport
        })
            .then(datas => {
                let { data, code } = datas
                if (code == "00000") {

                    setTrendCard(data)
                }


            })
        activeTimeLine({ ...body,activeId: path.id })
            .then(data => {
                setLoading(false)
                setLinData(data.data)
            })
    }

    const createPDF = async () => {
        notification.info({message:'Creating PDF...',description:"Please wait for a moment."})
        const pdf = new jsPDF("portrait", "pt", "a4");
        const data = document.querySelector("#report");
        const canvas = await html2canvas(data, {
            scale: 5, // 
            width: data.scrollWidth, // 
            height: data.scrollHeight // 
        });
        const imgData = canvas.toDataURL("image/png");
        pdf.addImage(imgData, "PNG", 0, 0, pdf.internal.pageSize.getWidth(), pdf.internal.pageSize.getHeight());
        pdf.save("report.pdf");
    };
    useEffect(() => {
        getList()
    }, [query,activeTab])

    const handleTimerClick = (row) => {
        debounce(() => {
            if (activeTab == "Day" || activeTab == "Daily") {
                setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: row.value.format('YYYY-MM-DD') })
            } else {
                setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: row?.endvalue?.format('YYYY-MM-DD') })
            }
        })
    }

    return (
        <Spin spinning={loading}>
            <div>
                <div className='pt-[10px] pb-[10px] flex justify-between'>
                    <div className='text-[20px] text-cblock font-semibold'>Report</div>
                    <div
                        onClick={createPDF}
                        className='text-[14px] text-cblock font-semibold h-[40px] rounded-[20px] flex items-center pl-[20px] pr-[20px] cursor-pointer ' style={{ 'border': ' 1px solid #333951' }} >Download</div>
                </div>
                <div className='flex mt-[20px]' style={{justifyContent:'center'}}>
                    <TabMenus type="report" menus={List} activeTab={activeTab} setActiveTab={setActiveTab} />
                </div>
                <div className='flex mt-[45px]' style={{justifyContent:'center',marginTop:'25px'}}>
                    <TimeSelection name={activeTab} handleClick={(e) => handleTimerClick(e)} />
                </div>
            </div>
            <div className=' mt-[44px] pl-[20px] pr-[20px]' id="report">
                <SleepScoreCard card={cards} isShow={false} activeTab={activeTab}/>

                <div>
                    <SleepSumLine isShow={false} data3={data3} activeTab={activeTab} />
                </div>
                <div className='mt-[50px]'>
                <BotvICom chatData={charData} SetIsShow={false} activeTab={mothObj[activeTab]} />
                    {/* <BotvITal card={card} chatData={charData} data={data} SetIsShow={false} /> */}
                </div>
                <div>
                    {/* <ActiviteScom cards={DailCards} chartData={DailChartData} SetIsShow={false} /> */}
                    <ActivitiesReport isReport={true} reportActiveTab={activeTab} dailChartData={DailChartData} formatDuration={formatDuration} calculateDuration={calculateDuration} isShow={isShow} SetIsShow={SetIsShow} />
                </div>
                {/* <div>
                    <WeeklyCom query={query} path={path} title="Activities" IsShow={false} activeTab={activeTab} />
                </div> */}
                <div className='mt-[50px]'>
                    <TrendCom data={TrendCard} setTrendMoudle={false} setCallMoudle={false} />
                </div>
                {/* <div>
                    {
                        activeTab === 'Daily' ? <ReportCom linData={linData} setlineOpen2={false} /> : null
                    }
                </div> */}
                <br/>
                <br/>
                {/* <div>
                    <HistableCom query={{}} setOpen2={false} />
                </div> */}

            </div>
        </Spin>
    )
}
export default Report;