import QiuChar from "@/Components/QiuChar";
const ComCard = ({
  priority,
  minutesTimerCount,
  reportTypeName,
  status = 0,
  bgColor,
}) => {
  let bg = "#F7F7FF"
    // priority == 1
    //   ? "rgba(250,181,21,.1)"
    //   : priority == 2
    //   ? "rgba(250, 75, 94, 0.10)"
    //   : "#F7F7FF";

  // if (bgColor) {
  //   bg = bgColor;
  // }

  return (
    <div
      style={{
        backgroundColor: bg
      }}
      className={`mr-[10px] w-[200px] h-[150px]  flex flex-col items-center rounded-t-[10px]`}
    >
      <div className="mt-[18px] w-[18px] h-[8px]  ">
        {/* <QiuChar status={status} /> */}
      </div>
      <div className="mt-[14px] text-cblock  text-[30px] font-bold  ">
        {" "}
        {minutesTimerCount}
      </div>
      <div className="mt-[13px] text-cblock  text-[14px] font-[500]  ">
        {reportTypeName}
      </div>
    </div>
  );
};
export default ComCard;
