
import React, { useEffect, useRef, useState } from 'react';
import { LeftOutlined, RightOutlined } from '@ant-design/icons';
import { Spin, Table, Tag } from 'antd';
import './SleepScore.css';
import ActivitiesChart from './ActivitiesChart'
import WeekCharts from './weekcharts.jsx';
import TabMenus from '@/Components/TabMenus/index'
import TimeSelection from '@/Components/TimeSelection/index'
import { dailyactivity } from '@/api/detailOfElderly'
import debounce from '@/utils/debounce'
import HistableCom from './HistableCom.jsx'
import HisTable from './assembly/HisTable'
import dayjs from 'dayjs'
import ModelTitle from '@/Components/ModelTitle/index'
import { useCountStore } from '@/store/index';
import HisCards from './assembly/HisCards'

const BioVitalSummary = (props) => {
    const Dailyreport = useCountStore(state => state.Dailyreport);
    const { isShow, SetIsShow, formatDuration, calculateDuration, isReport, reportActiveTab, dailChartData} = props
    const [loading, setLoading] = useState(true);
    const [series, setSeries] = useState([])
    console.log(reportActiveTab)
    console.log(dailChartData)
    function dayOfWeekFromDayNumber(dayNumber) {
        const daysOfWeek = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"];
        return daysOfWeek[dayNumber];
    }

    const [List, setList] = useState([

        {
            text: "Day",
            bo: true,
            warring: false
        },

        {
            text: "Week",
            bo: false,
            warring: false
        },
        {
            text: "Month",
            bo: false,
            warring: false
        }
    ]);
    const [chartData, setChartData] = useState([])
    const [cards, setCards] = useState([])
    const [activeTab, setActiveTab] = useState('Day')
    const [query, setQuery] = useState({
        "startTime": dayjs().format('YYYY-MM-DD'),
        "endTime": ""
    })

    const mergeDatasWeek = (data) => {
        // 
        const result = {};
    
        //  week 1-7 
        for (let i = 1; i <= 7; i++) {
            result[i] = {
                week: i,
                weekDetail: {},
                count: 0
            };
        }
    
        // 
        data?.forEach(item => {
            const { week, weekDetail } = item;
            if (!result[week]) {
                result[week] = { ...item, count: 1 };
            } else {
                Object.keys(weekDetail).forEach(key => {
                    if (!result[week].weekDetail[key]) {
                        result[week].weekDetail[key] = 0;
                    }
                    result[week].weekDetail[key] += weekDetail[key];
                });
                result[week].count += 1;
            }
        });
    
        // 
        Object.keys(result).forEach(week => {
            const { weekDetail, count } = result[week];
            if (count > 0) {
                Object.keys(weekDetail).forEach(key => {
                    weekDetail[key] = weekDetail[key] / count;
                });
            }
            delete result[week].count;
        });
    
        // 
        const mergedData = Object.values(result);
    
        return mergedData;
    }

    const mergeDatasMonth = (data) => {
        // 
        const result = {};

        // 
        data?.forEach(item => {
            const { monthday, monthDetail } = item;
            if (!result[monthday]) {
                result[monthday] = { ...item, count: 1 };
            } else {
                Object.keys(monthDetail).forEach(key => {
                    if (!result[monthday].monthDetail[key]) {
                        result[monthday].monthDetail[key] = 0;
                    }
                    result[monthday].monthDetail[key] += monthDetail[key];
                });
                result[monthday].count += 1;
            }
        });

        // 
        Object.keys(result).forEach(monthday => {
            const { monthDetail, count } = result[monthday];
            Object.keys(monthDetail).forEach(key => {
                monthDetail[key] = monthDetail[key] / count;
            });
            delete result[monthday].count;
        });

        // 
        const mergedData = Object.values(result);

        return mergedData
    }

    const init = () => {
        setLoading(true)
        setCards([])
        setSeries([])
        if(dailChartData) {
            handleDatas(dailChartData)
        }else {
            dailyactivity({
                ...query, ...Dailyreport,
                "radarType": 0
            })
                .then(data => {
                    try {
                        handleDatas(data.data)
                    } catch (error) {
                        setSeries([])
                    }
                    setLoading(false)
                })
        }
    }

    const handleDatas = (datas) => {
        console.log(datas)
        setChartData([])
        let { detailList, groupJSON, dailyReportsMonthDTOList, dailyReportsWeekDTOList } = datas
        console.log(groupJSON)
        let datalist = Object.values(detailList)
        datalist = datalist.map(e => {
            return {
                ...e,
                minutesTimerCount: formatDuration(e.minutesCount),
                valueStatu: e.valueStatu ? e.valueStatu : 0
            }
        })

        let timerdatas = calculateDuration(Object.values(groupJSON).flat(2))
        console.log('timerdatas', timerdatas)
        setChartData(timerdatas)
        setCards(datalist)
        console.log(activeTab)
        const cWeek = activeTab === 'Week' || reportActiveTab === 'Week'
        const cMonth = activeTab === 'Month' || reportActiveTab === 'Month'
        if (!reportActiveTab && (activeTab == "Day" || activeTab == "Daily") || (reportActiveTab && reportActiveTab === 'Daily')) {
            // todo
        }else {
            console.log(reportActiveTab)
            let dailyReportsDTOList = cWeek ? dailyReportsWeekDTOList : dailyReportsMonthDTOList
            console.log(dailyReportsDTOList)
            
            let mergeDatas = []
            if(cWeek) mergeDatas = mergeDatasWeek(dailyReportsDTOList)
            else if(cMonth) mergeDatas = mergeDatasMonth(dailyReportsMonthDTOList)
            console.log(mergeDatas)
            const arrayWeek = [];
            const leng = cWeek ? 7 : 31
            //  arrayWeek leng 
            for (let i = 0; i < leng; i++) {
                arrayWeek.push([]);
            }

            const keysArray = datalist.map(res=>res.reportType)
            mergeDatas.forEach(item => {
                console.log(item)
                for (let i = 0; i < leng; i++) {
                    const key = keysArray[i]
                    console.log(key)
                    let weekDetailValue = 0
                    if(cWeek) weekDetailValue = item?.weekDetail[key] || 0; // 0
                    else weekDetailValue = item?.monthDetail[key] || 0; // 0
                    arrayWeek[i].push(weekDetailValue);
                    console.log(weekDetailValue)
                }
            });
            console.log(keysArray)
            console.log(arrayWeek)
            const colors = datalist.map(item => item.color);
            let seriesArr = []
            // 
            const currentDate = new Date();
            let currentMonth = currentDate.getMonth() + 1;
            currentMonth = currentMonth < 10 ? '0' + currentMonth : currentMonth;
            seriesArr = mergeDatas.map((item,index) => {
                const xname = cWeek?dayOfWeekFromDayNumber(item.week-1) : currentMonth+'-'+(item.monthday < 10 ? '0' + item.monthday : item.monthday)
                console.log(xname)
                return {
                    name: xname,
                    type: 'bar',
                    stack: 'total',
                    // barWidth: '100px',
                    // label: {
                    //     show: true,
                    //     color: '#f2f2f2'
                    // },
                    itemStyle: {
                        color: colors[index]
                    },
                    data: arrayWeek[index]
                }

            })
            console.log(seriesArr)
            setSeries(seriesArr)
        }
        setLoading(false)
    }

    const handleTimerClick = (row) => {
        debounce(() => {
            if (activeTab == "Day") {
                setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: "" })
            } else {

                setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: row?.endvalue?.format('YYYY-MM-DD') })
            }
        })
    }
    useEffect(() => {
        if (isShow) {
            init()
        }
    }, [isShow, query, dailChartData])

    if(isReport) {
        return (
            <>  
                <div className='flex w-full relative'>
                    <div className='mt-[12px] text-[20px] text-cblock font-semibold ' style={{marginTop:'40px',marginBottom:'-36px'}}>Activities Report (ADL)</div>
                </div>
                <div className='pt-[50px] flex '>
                    <HisCards data={cards} />
                </div>
                <div className='bg-lightwhite'>
                    {
                        reportActiveTab == "Day" && <ActivitiesChart data={chartData} />
                    }
                    {   
                        reportActiveTab == "Week" && <WeekCharts proSeries={series}/>
                    }
                    {
                        reportActiveTab == "Month" && <WeekCharts proSeries={series} />
                    }

                    <div>
                        <HisTable data={cards} hideColumns={['target', 'trend', 'type']} />
                    </div>
                </div>
            </>
        )
    }else {
        return (
            <>
                {props.isShow &&
                    <div className='mode-back'>
                        <div className='sleepModel'>
                            <Spin spinning={loading} style={{marginTop: '200px'}}>
                                <ModelTitle title={"Activities Report (ADL)"} setIsShow={SetIsShow} />
                                <div className='flex flex-col items-center'>
                                    <div className='flex mt-[44px]'>
                                        <TabMenus menus={List} activeTab={activeTab} setActiveTab={setActiveTab} setLoading={setLoading}/>
                                    </div>
                                    <div className=' mt-[45px]  justify-center items-center'>
                                        <TimeSelection name={activeTab} handleClick={(e) => handleTimerClick(e)} />
                                    </div>
                                </div>
    
                                <div className='pt-[50px] flex '>
                                    <HisCards data={cards} />
                                </div>
                                <div className='bg-lightwhite' style={{ opacity: loading?0:1}}>
                                    {
                                        activeTab == "Day" && <ActivitiesChart data={chartData} />
                                    }
                                    {   
                                        activeTab == "Week" && <WeekCharts proSeries={series} activeTab={activeTab} />
                                    }
                                    {
                                        activeTab == "Month" && <WeekCharts proSeries={series} activeTab={activeTab} />
                                    }
    
                                    <div>
                                        <HisTable data={cards} hideColumns={['target', 'trend', 'type']} />
                                    </div>
                                </div>
                                {/* <HistableCom query={query} setOpen2={false} /> */}
                            </Spin>
                        </div>
                    </div >
                }
            </>
        )
    }
};

export default BioVitalSummary;
