// create alertCom component
import React, { useEffect, useState } from "react";

const svgs = import.meta.glob("./imgs/*.svg", { eager: true });
let obj = {};
for (const key in svgs) {
  if (Object.hasOwnProperty.call(svgs, key)) {
    const element = svgs[key];
    obj[key] = element.default;
  }
}

console.log("obj", obj);
export default function AlertCom({ value = 1, priority }) {
  //set useState color
  const [color, setColor] = useState("");
  useEffect(() => {
    if (priority == 0) {
      //
      setColor("#6FCF97");
    } else if (priority == 1) {
      setColor("#fab515");
      //
    } else if (priority == 2) {
      setColor("#fa4b5e");
    } else {
      setColor("#4285f4");
    }
  }, [priority]);
  return (
    <div
      style={{ background: color }}
      className="w-[32px] h-[32px] rounded-full  flex  justify-center items-center "
    >
      <img src={obj[`./imgs/${value}.svg`]} />
    </div>
  );
}
