
export default function SummaryBtn({text = "Summary", onClick = () => { }}) {

    return (
        <>
            <div onClick={() => onClick()}
                className='hover:opacity-70 cursor-pointer text-[14px] text-cblock font-semibold h-[40px] rounded-[20px] flex items-center pl-[20px] pr-[20px]' 
                style={{ 'border': ' 1px solid #333951' }} 
            >{text}
            </div>
        </>
    )
}