
import React, { useEffect, useRef, useState } from 'react';
import { LeftOutlined, RightOutlined } from '@ant-design/icons';
import { Spin, Table, Tag } from 'antd';
import TabMenus from '@/Components/TabMenus'
import TimeSelection from '@/Components/TimeSelection'
import ModelTitle from '@/Components/ModelTitle/index'
import { activeTimeLine, listByElder } from '@/api/detailOfElderly'
import TimelIneChar from './TimelIneChar.jsx'
import HistableCom from './HistableCom.jsx'
import { useCountStore } from '@/store/index';
import './SleepScore.css';
import dayjs from 'dayjs';

const SleepHistorySummary = ({ title, isShow, setIsShow, path, columns }) => {
    const [List, setList] = useState([
        {
            text: "Day",
            bo: true,
            warring: false
        },

        // {
        //     text: "Week",
        //     bo: false,
        //     warring: false
        // },
        // {
        //     text: "Month",
        //     bo: false,
        //     warring: false
        // }
    ]);
    const [loading, setLoading] = useState(true);
    const [activeTab, setActiveTab] = useState('Day')
    const [linData, setLineData] = useState([])
    const Dailyreport = useCountStore(state => state.Dailyreport);

    const [query, setQuery] = useState({
       
        "startTime": dayjs().format('YYYY-MM-DD'),
        "endTime": ""
    })

    const HandleTabText = (e) => {
        setActiveTab(e)
    }
    const handleTimerClick = (row) => {
        if (activeTab == "Day") {

            setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: row.value.format('YYYY-MM-DD') })
        } else {

            setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: row.endvalue ? row?.endvalue?.format('YYYY-MM-DD') : "" })
        }
    }
    const init = () => {
        if (query.startTime == "") return
        setLoading(true)
        activeTimeLine({
            ...query,
            ...Dailyreport
        })
            .then(datas => {
                let { data, code } = datas
                if (code == "00000") {

                    setLineData(data)
                }

                setLoading(false)
            })
    }
    useEffect(() => {
        if (isShow) {
            
            init()
        }
    }, [query])
    return (
        <div>{isShow &&
            <div className='mode-back'>
            <div className='sleepModel'>
                <Spin spinning={loading}>
                <ModelTitle title={title} setIsShow={setIsShow} />
                <div className='flex flex-col items-center'>


                    <div className='flex mt-[44px]'>
                        <TabMenus menus={List} activeTab={activeTab} setActiveTab={setActiveTab} />

                    </div>
                    <div className='mt-[45px]'>
                        <TimeSelection name={activeTab} handleClick={handleTimerClick} />
                    </div>
                </div>
                
                <div className='mt-[30px]' style={{ height: "450px" }}>
                    <TimelIneChar data={linData} />
                </div>
                {/* <HistableCom  query={query} setOpen2={false}/> */}
                </Spin>
            </div>
            </div>
        }</div>
    );
};

export default SleepHistorySummary;
