import React, { useState, useEffect, useRef } from 'react';
import ReactEcharts from 'echarts-for-react';
import { Spin } from 'antd';
import 'echarts/lib/chart/bar';
import iconUrl10 from '@/assets/icon/toilet-green.svg' //
import iconUrl11 from '@/assets/icon/svgred.svg' //
import iconUrl12 from '@/assets/icon/toilet-error.svg' //out of room

function TimelIneChar({ data }) {
    const elementRef = useRef(null);
    const [option, SetOption] = useState({})
    const [timeCates, setTimeCates] = useState([])
    const [spinning, setSpinning] = useState(true)
    const textFomat = (text, width) => {
        let formtText = text
        if (width < 20) {
            formtText = "..."
        } else {
            let len = formtText.length - 1
            if ((len * 9 + 20) > width) {
                let chushu = Math.floor((width - 20) / 10)

                if (chushu > 1) {
                    if (chushu < len) {
                        formtText = formtText.slice(0, chushu) + "..."
                    }
                } else {
                    formtText = "..."
                }

            }
        }
        return formtText
    }

    //0
    console.log(data)
    if(data.length === 1 && data[0].startTime === "18:00" && data[0].endTime === "18:00") {
        data[0].startTime = '00:00'
        data[0].endTime = '23:59'
    }else if(data.length) updateEvents(data)

    console.log(data)

    function convertTimeToMinutes(time) {
        let [hours, minutes] = time.split(":").map(Number);
        console.log(hours, minutes)
        return hours * 60 + minutes;
    }
    function updateEvents(events) {
        events.forEach((event,ri) => {
            if (ri===0 && convertTimeToMinutes(event.startTime) > convertTimeToMinutes(event.endTime)) {
                event.startTime = "00:00";
            }
        });
    }

    const updateTimeRanges = (timeRanges, heightIncrement = 1) => {
        // 
        timeRanges.sort((a, b) => a.start - b.start || a.end - b.end);

        // height0
        timeRanges.forEach(range => range.height = 0);

        // 
        let activeRanges = 0;
        // 
        let endActiveCounts = new Array(timeRanges.length).fill(0);

        for (let i = 0; i < timeRanges.length; i++) {
            let currentRange = timeRanges[i];
            activeRanges = 1; // 

            // 
            for (let j = 0; j < i; j++) {
                let previousRange = timeRanges[j];
                if (currentRange.start < previousRange.end) { // 
                    activeRanges = Math.max(activeRanges, endActiveCounts[j] + 1);
                }
            }

            // 
            endActiveCounts[i] = activeRanges;
            // height
            currentRange.height = (activeRanges - 1) * heightIncrement;
        }

        return timeRanges;
    }
    const timeToDecimal = (timeStr) => {
        const [hours, minutes] = timeStr.split(":").map(Number);
        return hours + minutes / 60;
    }

    const processData = (data, iconUrl, priorityFilter) => data
        // .filter(item => item.timeLine.length > 0 && priorityFilter.includes(item.priority))
        .flatMap(item => item.timeLine.map(timeLineItem => ({
            start: timeToDecimal(timeLineItem.startTime),
            end: timeToDecimal(timeLineItem.endTime),
            back: item.color,
            msg: '', //item.functionName,
            src: '' //item.iconUrl || iconUrl //  item.iconUrl
        })));

    const renderCustomSeriesItem = (params, api, timeSegments, offsetHeight, cindex = 1, cheight = 0) => {

        var start = api.value(1);
        var end = api.value(2);
        var height = 40; // 
        let index = params.dataIndexInside
        let row = timeSegments[index]
        let jsheight = row.height + (height / 2) + cheight
        var layer = api.value(3);

        var xValueStart = api.coord([start, layer])[0];
        var xValueEnd = api.coord([end, layer])[0];

        var style = api.style();
        style.fill = row.back; // 
        let y = (offsetHeight / 2 - height / 2) - (jsheight * cindex)
        let width = xValueEnd - xValueStart

        let text = row.msg
        text = textFomat(text, width)
        let children = [
            {
                type: 'rect',
                shape: {
                    x: xValueStart,
                    y,
                    width,
                    height,

                },
                style,
            },
            {
                type: 'text',
                style: {
                    x: xValueStart + (height / 2) + 5,
                    y: y + (height / 2) + 7,
                    textVerticalAlign: 'bottom',
                    textAlign: 'left',
                    text: text,
                    textFill: '#fff',
                },

            },
            {
                type: 'image',
                style: {
                    image: row.src,
                    x: xValueStart + (height / 2) - 15, // x
                    y: y + (height / 2) - 8, // y
                    width: 15,
                    height: 15
                }
            }

        ]
        console.log(';cheight', cheight)
        if (cheight == -20) {
            children.unshift({
                type: 'rect', // 
                shape: {
                    x: xValueStart, //  x 
                    y: 0, //  y 
                    width, // api.getWidth()
                    height: api.getHeight() // api.getHeight()
                },
                style: {
                    fill: '#333951' // 
                },
                z: 100 // 
            })
        }
        return {
            type: 'group',
            children
        };
    }

    const createSeriesData = (segments) => segments.map(seg => ({
        value: [seg.id, seg.start, seg.end, seg.layer],
        id: seg.id
    }));
    const init = () => {
        if (!data) return
        let ban = Math.floor(data.length / 2)
        // let data1 = data.slice(0,100)
        let data1 = data.map((e, index) => {

            if (ban <= index) {
                e.priority = 0
            } else {
                e.priority = 1
            }

            let backColor = '#65d5fd'
            if (e.type === 10) backColor = '#ffe753' //"Elderly sedentary"
            else if (e.type === 11) backColor = '#65d5fd' //"Elderly Activity"
            else if (e.type === 12) backColor = '#464c62' //"Elderly out of room"
            else if (e.type === 5) backColor = '#ff6161' //"Elderly out of bed"
            else if (e.type === 13) backColor = '#997fff' //"Elderly bathroom use"
            else if (e.type === 3) backColor = '#c1ff95' //"Elderly light sleep"
            else if (e.type === 4) backColor = '#408a0b' //"Elderly deep sleep"

            e.color = backColor
            e.functionName = e.typeName
            e.timeLine = [
                {
                    startTime: e.startTime,
                    endTime: e.endTime
                }
            ]

            return e
        })

        // "23:53" - "00:03"
        const resultTls = [];
        data1.forEach(interval => {
            const { startTime, endTime, ...rest } = interval;

            if (startTime > endTime) {
            // Split into two intervals
            const firstPart = {
                startTime: startTime,
                endTime: '23:59',
                ...rest,
                timeLine: [{ startTime: startTime, endTime: '23:59' }]
            };

            const secondPart = {
                startTime: '00:00',
                endTime: endTime,
                ...rest,
                timeLine: [{ startTime: '00:00', endTime: endTime }]
            };

            resultTls.push(firstPart, secondPart);
            } else {
            resultTls.push(interval);
            }
        });

        data1 = resultTls

        // data1 = data1.filter(res=>res.type === 11 && res.startTime > '12:00' && res.endTime < '13:00')
        // console.log(data1)


        //
        let type10 = [], type11 = [], type12 = [], type13 = [], type5 = [], type3 = [], type4 = []
        type10 = data1.filter(res=>res.type === 10)
        type11 = data1.filter(res=>res.type === 11)
        type12 = data1.filter(res=>res.type === 12)
        type13 = data1.filter(res=>res.type === 13)
        type5 = data1.filter(res=>res.type === 5)
        type3 = data1.filter(res=>res.type === 3)
        type4 = data1.filter(res=>res.type === 4)

        type10 = processData(type10, iconUrl10, [0]);
        type10 = updateTimeRanges(type10, 1000)
        type11 = processData(type11, iconUrl11, [0]);
        type11 = updateTimeRanges(type11, 1000)
        type12 = processData(type12, iconUrl12, [0]);
        type12 = updateTimeRanges(type12, 1000)
        type13 = processData(type13, iconUrl12, [0]);
        type13 = updateTimeRanges(type13, 1000)
        type5 = processData(type5, iconUrl12, [0]);
        type5 = updateTimeRanges(type5, 1000)
        type3 = processData(type3, iconUrl12, [0]);
        type3 = updateTimeRanges(type3, 1000)
        type4 = processData(type4, iconUrl12, [0]);
        type4 = updateTimeRanges(type4, 1000)

        // let timeDataArrTop = processData(data1, iconUrl, [0]);
        // let timeDataArrBottm = processData(data1, iconUrl, [1, 2]);
        // let timeDataArrOut = processData(data1, iconUrl, [3]);

        // let timeSegments1 = updateTimeRanges(timeDataArrTop, 100)
        // let timeSegments3 = updateTimeRanges(timeDataArrBottm, 100)
        // let timeSegments4 = updateTimeRanges(timeDataArrOut, 100)
        
        const { offsetWidth, offsetHeight } = elementRef.current;

        let option1 = {
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                    type: 'shadow'
                },
                formatter: function (params) {
                    var segment = params[0].data;
                    return 'Segment: ' + segment.id + '<br>Start: ' + segment.start + '<br>End: ' + segment.end;
                }
            },
            dataZoom: [
                {
                    type: "inside"
                }
            ],
            xAxis: {
                type: 'value',
                min: 0,
                max: 24,
                interval: 2,
                splitLine: {
                    show: false
                },
                axisLabel: {
                    formatter: function (value, index) {
                        let i = value
                        let hours = i < 10 ? '0' + i : i; // 100
                        let minutes = '00'; // 00
                        // console.log(`${hours}:${minutes}`);
                        return `${hours}:${minutes}`
                    }
                },
                splitArea: {
                    show: true,
                    color: ['#F7F7FF', '#f2f2fa']
                },



            },
            yAxis: {
                type: 'category',
                data: [] // Y
            },
            grid: {
                top: 0,
                bottom: 30,
                right: 30,
                left: 30
            },
            legend: {
                data: ['Sedentary', 'Activity', 'Out of room', 'Out of bed', 'Bathroom use', 'Light sleep', 'Deep sleep']
            },
            series: [
                {
                    name: 'Sedentary',
                    type: 'custom',
                    itemStyle: { color: '#ffe753' },
                    renderItem: (params, api) => renderCustomSeriesItem(params, api, type10, offsetHeight, 2.4),
                    data: createSeriesData(type10)
                },
                {
                    name: 'Activity',
                    type: 'custom',
                    itemStyle: { color: '#65d5fd' },
                    renderItem: (params, api) => renderCustomSeriesItem(params, api, type11, offsetHeight, 0),
                    data: createSeriesData(type11)
                },
                {
                    name: 'Out of room',
                    type: 'custom',
                    itemStyle: { color: '#464c62' },
                    renderItem: (params, api) => renderCustomSeriesItem(params, api, type12, offsetHeight,-2.2),
                    data: createSeriesData(type12)
                },
                {
                    name: 'Out of bed',
                    type: 'custom',     
                    itemStyle: { color: '#ff6161' },              
                    renderItem: (params, api) => renderCustomSeriesItem(params, api, type5, offsetHeight,-6.6),
                    data: createSeriesData(type5)
                },
                {
                    name: 'Light sleep',
                    type: 'custom',
                    itemStyle: { color: '#c1ff95' },     
                    renderItem: (params, api) => renderCustomSeriesItem(params, api, type3, offsetHeight,4.6),
                    data: createSeriesData(type3)
                },
                {
                    name: 'Deep sleep',
                    type: 'custom',
                    itemStyle: { color: '#408a0b' },     
                    renderItem: (params, api) => renderCustomSeriesItem(params, api, type4, offsetHeight,6.8),
                    data: createSeriesData(type4)
                },
                {
                    name: 'Bathroom use',
                    type: 'custom',
                    itemStyle: { color: '#997fff' },     
                    renderItem: (params, api) => renderCustomSeriesItem(params, api, type13, offsetHeight,-4.4),
                    data: createSeriesData(type13)
                },
            ],
            graphic: {
                elements: [
                    {
                        type: 'line',
                        shape: {
                            x1: 10, y1: 200, x2: offsetWidth, y2: 200
                        },
                        style: {
                            stroke: '#000',
                            lineWidth: 2,
                            lineHiehgt: 1
                        },
                        silent: true
                    },
                    // {
                    //     type: 'rect', // 
                    //     shape: {
                    //         x: 100,  //  x 
                    //         y: 0,   //  y 
                    //         width: 200, // 
                    //         height: offsetHeight // 
                    //     },
                    //     style: {
                    //         fill: 'rgba(0,0,0,0.5)' // 0.5
                    //     },
                    //     z: 100 // z  z 
                    // }
                ]
            }
        };
        SetOption(option1)

    }
    useEffect(() => {

        init()
        setTimeout(() => {
            setSpinning(false)
        }, 2000);


    }, [data])


    return <Spin spinning={spinning}><div className='w-full h-full' ref={elementRef} style={{marginBottom:'20px'}} >
        <ReactEcharts style={{ height: "450px"}} lazyUpdate={true} option={option} /></div></Spin>
}

export default TimelIneChar;