import React, { useState, useEffect } from 'react';
import { Spin } from 'antd';
import BioVitalSummary from './BioVitalSummary.jsx';
import SleepHistorySummary from './SleepHistorySummary.jsx';
import { getBioVtalSummary, getReportSummary } from '@/api/detailOfElderly'
// import BotvITal from './BotvITal'
import BotvICom from './BotvICom'
import BotWeekMonthCom from './BotWeekMonthCom'
import HistableCom from './HistableCom.jsx'
import { useCountStore } from '@/store/index';
import dayjs from 'dayjs'
import weekday from 'dayjs/plugin/weekday';
import weekOfYear from 'dayjs/plugin/weekOfYear';
import isoWeeksInYear from 'dayjs/plugin/isoWeeksInYear';

dayjs.extend(weekday);
dayjs.extend(weekOfYear);
dayjs.extend(isoWeeksInYear);

const Botvial = ({ activeTab }) => {
    const Dailyreport = useCountStore(state => state.Dailyreport);
    const [query, setQuery] = useState({
        "startTime": dayjs().format('YYYY-MM-DD'),
    })
    const [isShow, SetIsShow] = useState(false)
    const [isShow1, SetIsShow1] = useState(false)
    const [activeTab1, setActiveTab1] = useState('Heart Rate')
    const [modelRange, setModelRange] = useState("Day")
    const [botWeekC, setBotWeekC] = useState({})
    const [chatData, setchatData] = useState({})
    const [loading, setLoading] = useState(false)

    const init = () => {
        if (activeTab == "Bio-Vital") {
            setLoading(true)
            Promise.all([
                getBioVtalSummary({
                    ...query,
                    ...Dailyreport
                }),
                getReportSummary(),
                getBioVtalSummary({
                    ...query,
                    ...Dailyreport,
                    startTime: dayjs().weekday(1).format('YYYY-MM-DD') ,
                    endTime:  dayjs().weekday(7).format('YYYY-MM-DD') 
                })

            ])
                .then(res => {
                    setLoading(false)
                        
                        let data1 = res[0]?.data
                        setchatData(data1)
                        let data2 = res[2]?.data
                        setBotWeekC(data2)
                        
                   
                })

        }

    }
    const handleModel = (bo, range) => {

        SetIsShow(bo)
        setModelRange(range)
    }
    useEffect(() => {
        init()
    }, [activeTab])
    useEffect(() => {

    }, [activeTab1])
    return (
        <Spin spinning={loading}>
            <BotvICom chatData={chatData} SetIsShow={SetIsShow} activeTab={"Day"} />
            {/* <BotvITal chatData={chatData} SetIsShow={SetIsShow} /> */}
            <div className='pt-[50px]'>
                <BotWeekMonthCom data={botWeekC} status={0} SetIsShow={handleModel}  activeTab={"Week"}/>
            </div>
            {/* <HistableCom query={{}} setOpen2={SetIsShow1} /> */}
            <BioVitalSummary rang={modelRange} isShow={isShow} SetIsShow={SetIsShow} />
            <SleepHistorySummary isShow={isShow1} setIsShow={SetIsShow1} />
        </Spin>
    );
};

export default Botvial;
