import React, { useState, useEffect } from 'react';
import { Spin } from 'antd';
import SleepScore from './SleepScore.jsx';
import SleepSummary from './SleepSummary.jsx';
import { sleepScore, getReportSummary, sleepSummary } from '@/api/detailOfElderly'
import { formatDuration } from '@/utils/seenYouUtil'
import SleepHistorySummary from './SleepHistorySummary.jsx'
import dayjs from 'dayjs'
import HistableCom from './HistableCom.jsx'
import SleepScoreCard from './SleepScoreCard.jsx'
import SleepSumLine from './SleepSumLine.jsx'
import { targetFormat } from '@/utils/comFunction.js'
import { useCountStore } from '@/store/index';
const Sleep = ({ activeTab }) => {
    const Dailyreport = useCountStore(state => state.Dailyreport);
    const [open, setOpen] = useState(false)
    const [open1, setOpen1] = useState(false)
    const [open2, setOpen2] = useState(false)
    const [query, setQuery] = useState({
        "startTime": dayjs().format('YYYY-MM-DD'),
        // startTime: "2024-04-16",
        "endTime": ""
    })
    const [card, setCard] = useState([])
    const [lines, setLines] = useState([])

    const [data3, setData3] = useState([])
    const [loading, setLoading] = useState(false)

    const calculateProportions = (data) => {
        //  dayjs  Date 
        data.forEach(item => {
            item.reportStartTime = dayjs(item.reportStartTime).valueOf();
            item.reportEndTime = dayjs(item.reportEndTime).valueOf();
        });

        // 
        data.forEach(item => {
            item.duration = item.reportEndTime - item.reportStartTime;
        });

        // 
        let totalDuration = data.reduce((total, item) => total + item.duration, 0);

        // 
        data.forEach(item => {
            item.proportion = item.duration / totalDuration;
        });

        return data;
    }
    const init = () => {
        if (activeTab == "Sleep") {
            setLoading(true)
            Promise.all([
                sleepScore({
                    ...query,
                    ...Dailyreport
                }),
           
                sleepSummary({
                    ...query,
                    ...Dailyreport
                })
            ])
                .then(res => {
                    setLoading(false)
                    let dataInfo = res[0]?.data?.sleepScoreDailyVO?.detailInfo
                   
                    setCard(res[0]?.data)
                    setData3(res[1])

                })
        }
    }
    useEffect(() => {

        init()
    }, [activeTab])
    return (

        <Spin spinning={loading}>
            {/* <div className='pt-[50px] pb-[10px] text-[20px] text-cblock font-semibold'>Sleep Report</div>
            <div className='pt-[10px] pb-[50px]'>
                <div className='w-full bg-onBackWarring p-[20px]  rounded-[20px] flex items-center flex-col' style={{ "box-shadow": "0px 0px 50px -10px rgba(0, 0, 0, 0.15)" }} >

                    <div className='w-[36px] h-[36px] rounded-[100px] bg-white flex justify-center items-center'>
                        <div dangerouslySetInnerHTML={{
                            __html: `<svg xmlns="http://www.w3.org/2000/svg" width="16" height="14" viewBox="0 0 16 14" fill="none">
                            <path d="M15.374 11.3978L9.35482 1.4154C9.11768 1.02214 8.6851 0.777832 8.22586 0.777832C7.76663 0.777832 7.33405 1.02214 7.09688 1.41542L1.0777 11.3978C0.832624 11.8043 0.825372 12.3135 1.05876 12.7268C1.2922 13.1401 1.73203 13.3968 2.20666 13.3968H14.2451C14.7197 13.3968 15.1596 13.1401 15.393 12.7268C15.6264 12.3134 15.6191 11.8042 15.374 11.3978ZM14.5611 12.257C14.4968 12.3707 14.3757 12.4414 14.2451 12.4414H2.20666C2.07599 12.4414 1.9549 12.3708 1.89067 12.257C1.82641 12.1432 1.82842 12.003 1.89586 11.8912L7.91509 1.90877C7.98038 1.80051 8.09947 1.73325 8.22589 1.73325C8.35229 1.73325 8.47137 1.80051 8.53666 1.90877L14.5558 11.8912C14.6233 12.003 14.6253 12.1432 14.5611 12.257Z" fill="#FA4B5E"/>
                            <path d="M8.23006 4.7085C7.86664 4.7085 7.58301 4.90351 7.58301 5.24921C7.58301 6.304 7.70708 7.8197 7.70708 8.87451C7.70711 9.14928 7.94646 9.2645 8.23009 9.2645C8.44281 9.2645 8.74418 9.14928 8.74418 8.87451C8.74418 7.81973 8.86825 6.30402 8.86825 5.24921C8.86825 4.90353 8.57577 4.7085 8.23006 4.7085Z" fill="#FA4B5E"/>
                            <path d="M8.23917 9.87622C7.84918 9.87622 7.55664 10.1864 7.55664 10.5588C7.55664 10.9222 7.84915 11.2413 8.23917 11.2413C8.60259 11.2413 8.91285 10.9222 8.91285 10.5588C8.91285 10.1864 8.60257 9.87622 8.23917 9.87622Z" fill="#FA4B5E"/>
                            </svg>` }}>

                        </div>
                    </div>
                    <div className='mt-[10px] text-white text-[16px] font-semibold '>
                        Heart Rate was too High.
                    </div>
                    <div className='mt-[18.5px] text-[14px] text-white '>Today, 20:57</div>

                </div>
            </div> */}
            <SleepScoreCard card={card} isShow={true} setOpen={setOpen} activeTab={"Day"} />
            <SleepSumLine setOpen={setOpen1} isShow={true} data3={data3} activeTab={"Day"} />
            {/* <div className='  '>
                <HistableCom query={{}} setOpen2={setOpen2} />
                </div> */}
            <SleepScore isShow={open} setIsShow={setOpen} />
            <SleepSummary isShow={open1} setIsShow={setOpen1} calculateProportions={calculateProportions} />
            <SleepHistorySummary
                title={"Sleep History Summary"}
                isShow={open2}
                setIsShow={setOpen2} />
        </Spin>
    );
};

export default Sleep;
