import React, { useState, useEffect } from 'react';
import ReactEcharts from 'echarts-for-react';
import 'echarts/lib/chart/bar';

function ActivitiesChart({ data }) {
    console.log(data)
    const [option1, setOption1] = useState(
        {}

    )
    useEffect(() => {

        if (data.length > 0) {

            setOption1({

                series: [
                    {
                        name: 'Access From',
                        type: 'pie',
                        radius: ['60%', '80%'],
                        avoidLabelOverlap: false,
                        label: {
                            show: false,
                            position: 'center'
                        },
                        startAngle: 270,
                        emphasis: {
                            label: {
                                show: true,
                                fontSize: 30,
                                fontWeight: 'bold'
                            }
                        },
                        labelLine: {
                            show: false
                        },
                        data: data
                        //  data:[data[0],data[1],data[2]]
                    },
                    {
                        name: 'hour',
                        type: 'gauge',
                        startAngle: 90,
                        endAngle: -270,
                        min: 0,
                        max: 12,
                        splitNumber: 12,
                        clockwise: true,
                        pointer: {
                            show: false
                        },
                        splitLine: {
                            show: false
                        },
                        axisTick: {
                            splitNumber: 1,
                            lineStyle: {
                                color: "#b5bcd5"
                            }
                        },
                        axisLine: {
                            lineStyle: {
                                width: 15,
                                color: [[1, 'rgba(0,0,0,0)']],
                                shadowColor: 'rgba(0, 0, 0, 0)',
                                shadowBlur: 15
                            }
                        },

                        axisLabel: {
                            fontSize: 10,
                            distance: 25,
                            color: "#b5bcd5",
                            padding: [0, 0, 0, 0],
                            formatter: function (value) { //
                                if (value == 12) {
                                    return 12
                                } else if (value == 3) {
                                    return 18
                                } else if (value == 6) {
                                    return 12
                                } else if (value == 9) {
                                    return 6
                                } else {
                                    return ""
                                }
                            }
                        },


                        detail: {
                            show: false
                        },
                        title: {
                            offsetCenter: [0, '30%']
                        },
                        data: [
                            {
                                value: 0
                            }
                        ]
                    },


                ]
            })
        } else {
            setOption1({
                title: {
                    text: "There is currently no data available",
                    show: true,
                    left: "center",
                    top: "center",
                    textStyle: {
                        color: "#f8f8f8",
                        fontSize: "20px",
                        fontWeight: 400,
                    },
                },
            })
        }
    }, [data])


    return <div className=''> <ReactEcharts notMerge={true} option={option1} /></div>;
}

export default ActivitiesChart;