
import React, { useEffect, useRef, useState } from 'react';

import './SleepScore.css';
import dayjs from 'dayjs'

import TabMenus from '@/Components/TabMenus/index'
import TimeSelection from '@/Components/TimeSelection/index'
import debounce from '@/utils/debounce'
import HistableCom from './HistableCom.jsx'
import WeeklyCom from './weeklyCom.jsx'
import { useCountStore } from '@/store/index';
import ModelTitle from '@/Components/ModelTitle/index'
const ActiveitiesModel = (props) => {
    const Dailyreport = useCountStore(state => state.Dailyreport);
    const { SetIsShow, } = props
    const [activeTab1, setActiveTab1] = useState('Sedentary')
    const [List, setList] = useState([
        {
            text: "Day",
            bo: true,
            warring: false
        },

        {
            text: "Week",
            bo: false,
            warring: false
        },
        {
            text: "Month",
            bo: false,
            warring: false
        }
    ]);
    const [activeTab, setActiveTab] = useState('Day')
    const [query, setQuery] = useState({

        "startTime": dayjs().format('YYYY-MM-DD'),
        "endTime": "",
        "dateType": "day"
    })
    const [Weeklydata1, setWeeklydata1] = useState([
        {
            name: "Maximum Heart rate on 18/12/12",
            target: "",
            value: "111",
            type: "card",

        }
    ])

    const HandleTabText = (e) => {
        setActiveTab(e)
    }
    const handleTimerClick = (row) => {
        debounce(() => {
            let query1
            if (activeTab == "Day") {
                query1 = {
                    ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: "",
                    dateType: activeTab
                }

            } else {

                query1 = {
                    ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: row?.endvalue?.format('YYYY-MM-DD'),
                    dateType: activeTab
                }

            }
            setQuery({ ...query1, ...Dailyreport })
        })
    }

    return (
        <>

            {props.isShow &&
                <div className='mode-back'>
                    <div className='sleepModel'>

                        <ModelTitle title={"Activities"} setIsShow={SetIsShow} />
                        <div className='flex flex-col items-center'>

                            <div className='flex flex-col items-center' >

                                <div className='flex mt-[44px]'>
                                    <TabMenus menus={List} activeTab={activeTab} setActiveTab={setActiveTab} />
                                </div>
                                <div className=' mt-[45px]  justify-center items-center'>
                                    <TimeSelection name={activeTab} handleClick={(e) => handleTimerClick(e)} />
                                </div>
                            </div>
                        </div>

                        <WeeklyCom activeTab={activeTab} query={query} title="Activities" IsShow={false} />



                        {/* <HistableCom query={query} setOpen2={false} /> */}


                    </div>
                </div>
            }
        </>
    );
};

export default ActiveitiesModel;
