import HistoryTable from './assembly/HistoryTable'
import SummaryBtn from '@/Components/SummaryBtn'
import { reportHistory, OvreportHistory, NewOvreportHistory } from '@/api/AdminSupportAgents'
import { alertHistory } from '@/api/AdminSupportAgents'

import { useState, useEffect } from 'react'
import debounce from '@/utils/debounce'
import { useCountStore } from '@/store/index';
import dayjs from 'dayjs'
import { Spin } from 'antd';
import ls from 'store2'

export default function HistableCom({open2, setOpen2, isShow, query = {}, status = "",activeTab="" }) {
    const [hisLoading, setHisLoading] = useState(true)
    const [params, setParams] = useState({
        elderRocmRadarId: "1",
        radarId: "1",
         startTime: dayjs().subtract(10, 'day').format('YYYY-MM-DD'),
        //"startTime": "2024-04-18",
        endTime: dayjs().format('YYYY-MM-DD')
    })
    const [data, setData] = useState([])
    const [data5, setData5] = useState([])
    
    const Dailyreport = useCountStore(state => state.Dailyreport);
    useEffect(() => {
        console.log(query)
        console.log(11111)
        debounce(() => {
            // setHisLoading(true)
            let api
            if (status == "Overview") {

                api = NewOvreportHistory
            } else {

                api = reportHistory

            }
            api({ ...params, ...query, ...Dailyreport })
                .then(data => {
                    let datas = data.data
                    setData(datas)
                    datas = datas.filter(res=>res.type !== 1)
                    let data5 = datas.slice(0,5)
                    setData5(data5)
                    setHisLoading(false)
                    ls.session.set('alertLists',datas)
                })
        })

    }, [query])

    const opemPop = () =>{
        ls.session.set('ovHistablePop',1)
        setOpen2(true)
    }

    return <>
        <Spin spinning={hisLoading}>
        <div className='pt-[20px] pb-[10px] flex justify-between'>
            <div className='text-[20px] text-cblock font-semibold'>History</div>
            {setOpen2 && <SummaryBtn onClick={opemPop} />}
        </div>
        <div>
        <HistoryTable activeTab={activeTab}  data={data5} datalong={data} isShow={isShow} status={status} open2={open2} />
        </div>
        </Spin>
    </>
}