import SummaryBtn from '@/Components/SummaryBtn/index'
import React, { useEffect, useRef, useState } from 'react';
import TabMenus from '@/Components/TabMenus'
import WeeklyChar from './WeeklyChar'
import HisTable from './assembly/HisTable'
import avgheart from '@/assets/icon/avgheart.svg'
import avgRate from '@/assets/icon/avgRate.svg'
import dayjs from 'dayjs'
import { Spin, Table, Tag } from 'antd';
import HisCards from './assembly/HisCards'
import { targetFormat, setStatusFormate } from '@/utils/comFunction'
export default function BotWeekMonthCom({ data, typeName, isCard = false, SetIsShow = "", activeTab, title = "Weekly Summary", status = 0 }) {
    console.log(typeName)
    const initbo = useRef(false)
    const [menus1, setMenus1] = useState([
        {
            text: "Heart Rate",
            bo: true,
            warring: false
        },
        {
            text: "Respiratory Rate",
            bo: true,
            warring: false

        },

    ])
    const [loading, setLoading] = useState(true)
    const [activeTab1, setActiveTab1] = useState('Heart Rate')
    const [cheartInfo, setHeartInfo] = useState({})
    const [crespiratory, setRespiratory] = useState({})
    const [info, setInfo] = useState({})
    const [card, setCard] = useState({})
    const setChar = (config, color, tag) => {
        let typeName = tag === 1 ? 'Heart Rate' : 'Respiratory Rate'

        if (!config) return
        const { topList, allAvgNumber, avg, min, top } = config

        if(activeTab=== 'Week') topList.sort((a, b) => a.week - b.week);
        else if(activeTab=== 'Month') topList.sort((a, b) => a.month - b.month);

        let { maxArray, minArray } = findMinMaxValues(topList)

        let xdata = topList.map(e => {
            return e.week || e.month
        })
        let mindata = topList.map(e => {
            return e.number.min
        })
        let maxdata = topList.map(e => {
            return e.number.max - e.number.min

        })

        let arr = []
        for (const key in config) {
            if (isObject(config[key])) {
                let element = config[key];

                if (key == "avg") {
                    element.src = avgRate
                    element.reportTypeName = "Average Weekly " + typeName
                    element.cardTypeName = "Avg. Weekly " + typeName
                } else if (key == "top") {
                    element.src = avgheart
                    element.reportTypeName = `Maximum ${typeName} on ${dayjs(element.time).format("YY/MM/DD")}`

                    element.cardTypeName = "Maximum " + typeName
                } else { //min
                    element.cardTypeName = "Minimum " + typeName
                    element.src = avgheart
                    element.reportTypeName = `Minimum ${typeName} on ${dayjs(element.time).format("YY/MM/DD")}`
                }
                // targetFormat
                element.targetValue = targetFormat(element)
                element.color = undefined
                element.ctrendStatu = setStatusFormate(element.trendStatu)
                arr.push(element)
            }
        }

        let arr1 = [...arr]
       
        return {
            char: {
                maxArray,
                minArray,
                xdata,
                mindata,
                maxdata,
                allAvgNumber: allAvgNumber,
                color
            },
            list: arr,
            card:arr1.map(e => {
                e.valueStatu = e.ctrendStatu
                e.reportTypeName = e.cardTypeName
                return e
            })
        }


    }
    function isObject(value) {
        return typeof value === 'object' && value !== null && !Array.isArray(value);
    }
    function findMinMaxValues(topList) {
        let min_value = Infinity;
        let max_value = -Infinity;
        let min_index = -1;
        let max_index = -1;
        let len = topList.length

        topList.forEach((item, index) => {
            if (item.number.min < min_value) {
                min_value = item.number.min;
                min_index = index;
            }
            if (item.number.max > max_value) {
                max_value = item.number.max;
                max_index = index;
            }
        });
        const minArray = new Array(len).fill(undefined);
        const maxArray = new Array(len).fill(undefined);
        minArray[min_index] = min_value
        maxArray[max_index] = max_value
        return {
            maxArray, minArray
        }

        // return {
        //   minValue: min_value,
        //   minIndex: min_index,
        //   maxValue: max_value,
        //   maxIndex: max_index
        // };


    }
    const init = () => {
      if(!data )return
        const { heartInfo, respiratory } = data
        let heartInfoobj = setChar(heartInfo, "#FA4B5E", 1)
        let hrespiratoryobj = setChar(respiratory, "#FAB515", 2)
        setHeartInfo(heartInfoobj)
        setRespiratory(hrespiratoryobj)

        if (status == 0 || status == 1) {
            setInfo(heartInfoobj)
        } else {

            setInfo(hrespiratoryobj)

        }
        let t = setTimeout(() => {
            initbo.current = true
            clearTimeout(t)
        }, 300);

    }
    useEffect(() => {
        init()
    }, [data])
    useEffect(() => {
        if (initbo.current) {
            if (activeTab1 == "Heart Rate") {
                setInfo(cheartInfo)
            } else {
                setInfo(crespiratory)
            }
        }
    }, [activeTab1])
    return <>
        <div className='pt-[50px]'>
            <div className=' pb-[10px] flex justify-between'>
                <div className='text-[20px] text-cblock font-semibold'>{title}</div>
                {/* <SummaryBtn onClick={() => SetIsShow(true)} /> */}

            </div>
            {isCard && <>
                <div className='pt-[10px] flex '>
                    {
                        typeName === 'Heart Rate' ? <HisCards data={info?.card} typeName='Heart Rate' /> :
                        <HisCards data={info?.card} typeName='Respiratory Rate' />
                    }
                    
                </div>
            </>}
            {
                !isCard && <>
                    <div className='bg-lightwhite pl-[20px] pt-[40px] pr-[20px]'>
                        <div className=''>
                            <TabMenus menus={menus1}
                                activeTab={activeTab1}
                                setActiveTab={setActiveTab1} />
                        </div>
                    </div>
                </>
            }
            <div>
                <WeeklyChar cheartInfo={info} activeTab={activeTab} activeTab1={activeTab1} />
                <HisTable data={info?.list} bo={false}  hideColumns={['target','trend','type']} typeName={typeName} />
            </div>
        </div>
    </>
}