import React, { useState, useEffect, useRef } from 'react';
import useWebSocket from 'react-use-websocket';
import { commentsSend, commentsPage } from '@/api/detailOfElderly'
import debounce from '@/utils/debounce'
import dayjs from 'dayjs';
import ls from 'store2'
import { Spin } from 'antd'
import { setToken, removeToken, getToken } from '@/utils/auth'
const Comments = ({ path, activeTab }) => {
    // 
    let user = ls.get("user")

    const [messages, setMessages] = useState([]);
    const [message, setMessage] = useState('')
    const [total, setTotal] = useState(0)
    const [query, setQuery] = useState({ "current": 1, size: 10, elderId: path.id })
    const [isLoading, setIsLoading] = useState(false); // 
    const scrollableDivRef = useRef(null); // 

    const endOfMessagesRef = useRef(null);

    // WebSocketURLWebSocket

    const { sendMessage, lastMessage } = useWebSocket(import.meta.env.VITE_APP_BASE_WS + getToken(), {
        // 
        onMessage: (event) => {

            const getmessage = JSON.parse(event.data)

            getmessage.isMy = 0
            getmessage.gmtCreate = dayjs(getmessage.gmtCreate).format('YYYY-MM-DD HH:mm:ss')
            setMessages([...messages, getmessage]);
            scrollToBottom()

        },
        // 
        shouldReconnect: (closeEvent) => true,
    });

    // useEffect(() => {
    //     scrollToBottom()
    // }, [messages])
    // 
    const handleKeyDown = async (event) => {
        if (event.key === 'Enter') {

            event.preventDefault(); // Enter
            let obj = { elderId: path.id, message: message, isMy: 1, commentUserRole: user.role, commentUserName: user.lastName, gmtCreate: dayjs().format('YYYY-MM-DD HH:mm:ss') }
            let data = await commentsSend(obj)
            console.log("Data", data)
            if (data.code == "00000") {
                setMessages([...messages, data.data]);
                setMessage(''); // 
                scrollToBottom()
            }


        }
    };
    const getList = () => {
        debounce(() => {
            setIsLoading(true); // 
            commentsPage({ ...query }).then(res => {
                let datas = res.data
                const oldScrollHeight = scrollableDivRef.current.scrollHeight;
                const scrollTopBeforeLoading = scrollableDivRef.current.scrollTop;
                setMessages([...datas.records, ...messages])
                setTotal(datas.total)

                if (query.current == 1) {
                    scrollToBottom()
                } else {
                    requestAnimationFrame(() => {
                        // Step 4: 
                        const newScrollHeight = scrollableDivRef.current.scrollHeight;
                        const scrollOffset = newScrollHeight - oldScrollHeight;
                        scrollableDivRef.current.scrollTop = scrollTopBeforeLoading + scrollOffset;
                    });
                }
                setTimeout(() => {
                    setIsLoading(false); // 
                }, 1000);



            })
        })
    }
    const scrollToBottom = () => {
        let t = setTimeout(() => {
            endOfMessagesRef.current?.scrollIntoView(false);
            clearTimeout(t)
        }, 0);


    };

    useEffect(() => {
        getList()
    }, [activeTab, query])
    // 
    useEffect(() => {
        const handleScroll = () => {
            if (scrollableDivRef.current.scrollTop === 0 && !isLoading) {

                if (total > messages.length) {
                    setQuery({ ...query, current: query.current + 1 })
                }
            }
        };

        const div = scrollableDivRef.current;
        div.addEventListener('scroll', handleScroll);

        return () => div.removeEventListener('scroll', handleScroll); // 
    }, [isLoading]); // isLoading
    return (
        <>
            <div className=" text-[20px] font-[600]">Comments</div>
            <div className='mt-[10px] bg-lightwhite w-full min-h-[50px]  '>
                <div className="p-[20px]">
                    <Spin spinning={isLoading}>
                        <div ref={scrollableDivRef} className=" h-[467px] relative overflow-auto" >
                            {
                                messages.map((item, index) => {
                                    return (
                                        <div className={`mb-[20px] w-full flex ${item.isMy == 1 ? "justify-end  " : "justify-start"}`}>
                                            <div className={`w-[306px] `}   >
                                                <div className={` ${item.isMy == 1 ? "bg-[#4285F4] text-white" : "bg-[#eee]"} rounded-[10px]  text-[14px] text-primary font-[500]  pt-[9px] pb-[9px] pl-[21px] pr-[21px] radius-[10px]`}>
                                                    {item.message}
                                                </div>
                                                <div className="w-full flex justify-between text-[12px] text-primary " style={{padding:'6px 0'}}>
                                                    <div className=" font-[700] w-[170px] line-1 ">{item.commentUserRole} ({item.commentUserName})</div>
                                                    <div className="opacity-50">{item.gmtCreate}</div>
                                                </div>
                                            </div>
                                        </div>
                                    )
                                })
                            }
                            <div ref={endOfMessagesRef} />


                            <div style={{margin:'200px',textAlign:'center',color:'#888'}}>
                                {
                                    messages.length === 0 ? 'No Data' : null
                                }
                            </div>
                        </div>
                    </Spin>
                </div>

                <div className='w-full ' >
                    <div className='h-[1px]  bg-[#EBEBEB] w-full'></div>
                </div>
                {
                    user?.role == "Support Agent" ? <>
                        <div className='p-[14px]'>
                            <div className='rounded-[20px] pt-[10px] pb-[10px] pl-[29px] pr-[29px]' style={{ border: "1px solid #E0E0E0" }}>
                                <input
                                    disabled={user.role != "Support Agent"}
                                    placeholder='Please enter'
                                    value={message}
                                    onChange={(event) => setMessage(event.target.value)}
                                    onKeyDown={handleKeyDown}
                                    className='w-full h-full bg-lightwhite focus:outline-none' />
                            </div>
                        </div>
                    </> : <></>
                }

            </div>
        </>
    )
}
export default Comments