import React, { useEffect, useState } from "react";
import ReactEcharts from "echarts-for-react";
import "echarts/lib/chart/bar";
import ic_pill from "@/assets/ic_pill.svg";

function MyChart({ charData }) {
  const [option, setOption] = useState({});
  useEffect(() => {
    if (charData && charData.length > 0) {
      MyChartinit();
    } else {
      MyChartinit();
      //   setOption({
      //     title: {
      //       text: "There is currently no data availsssable",
      //       show: true,
      //       left: "center",
      //       top: "center",
      //       textStyle: {
      //         color: "#000",
      //         fontSize: "20px",
      //         fontWeight: 400,
      //       },
      //     },
      //   });
    }
  }, [charData]);
  const defaultCharData = [
    {
      name: "Demo Series 1",
      timer: ["2023-01-01", "2023-01-02", "2023-01-03"],
      array: [120, 132, 101],
      value: 118,
      src: ic_pill,
    },
    {
      name: "Demo Series 2",
      timer: ["2023-01-01", "2023-01-02", "2023-01-03"],
      array: [220, 182, 191],
      value: 198,
      src: ic_pill,
    },
  ];
  const MyChartinit = () => {
    setOption({
      backgroundColor: "rgb(247, 247, 255)",
      grid: {
        top: "13%",
        left: "3%",
        bottom: "8%",
        right: "3%",
      },
      tooltip: {
        trigger: "axis",
      },
      legend: {
        show: false,
      },

      xAxis: {
        type: "category",
        boundaryGap: false,
        data: defaultCharData[0].timer,
        splitLine: {
          show: false, //  x 
        },
      },

      yAxis: {
        name: "bpm", // 
        nameLocation: "end", // 'end' 
        nameTextStyle: {
          // 
          color: "#333",
          fontSize: 12,
          padding: [0, 0, 10, -10], // 
        },
        splitLine: { show: false },
        axisLine: { show: true },
        type: "value",
        axisLabel: {
          color: "#000",
        },
      },
      color: ["#FAB515", "#FA4B5E"],
      series: defaultCharData.map((item, index) => {
        return {
          name: item.name,
          type: "line",
          data: item.array,
          symbol: "none", // 
          smooth: true,
          markPoint: {
            label: {
              show: true,
              color: "#000",
              position: "right",
              backgroundColor: "#fff",
              fontSize: "12px",
              fontWeight: "bold",
            },
            data: [
              {
                name: "",
                coord: [Math.floor(item.array.length / 2), item.value],
                value: item.name,
              },
            ],
            symbolSize: [30, 30],
            symbol: `image://${item.src}`,
          },
          markLine: {
            symbol: "none",
            lineStyle: {
              type: "solid", // 
            },
            label: {
              show: false,
            },
            data: [{ value: item.value, name: "Average", yAxis: item.value }],
          },
        };
      }),
    });
  };

  return (
    <div className="bg-lightwhite pt-[20px]">
      <ReactEcharts option={option} notMerge={true} />
    </div>
  );
}

export default MyChart;
