
import React, { useEffect, useState } from 'react';
import TabMenus from '@/Components/TabMenus'
import ActiveChar from './ActiveChar'
import SummaryBtn from '@/Components/SummaryBtn'
import debounce from '@/utils/debounce'
import { activitySendatry, bathroomUse, activityActivity, activityoutOfBed, activitynightActivity } from '@/api/detailOfElderly'
import dayjs from 'dayjs'
import HisTable from './assembly/HisTable.jsx'
import { formatDuration } from '@/utils/seenYouUtil.js'
import toiletError from '@/assets/icon/toilet-error.svg'
import toiletGreen from '@/assets/icon/toilet-green.svg'
import ActivWeekChart from './ActivWeekChart'
import { useCountStore } from '@/store/index';
import { Spin } from 'antd';

const WeeklyCom = ({ setIsShow, title = "Weekly Summary", IsShow = true, query, activeTab }) => {
    const Dailyreport = useCountStore(state => state.Dailyreport);
    const [activeTab1, setActiveTab1] = useState('Sedentary')
    const [loading, setLoading] = useState(true);

    const [menus1, setMenus1] = useState([
        {
            text: "Sedentary",
            bo: true,
            warring: false
        },
        {
            text: "Bathroom Activity",
            bo: true,
            warring: false

        },
        // {
        //     text: "Activity",
        //     bo: true,
        //     warring: false

        // },
        // {
        //     text: "Out of Bed",
        //     bo: true,
        //     warring: false
        // },
        {
            text: "Night Activity",
            bo: true,
            warring: false

        },


    ])

    const [charData, setCharData] = useState({
        seriesdata: [],
        color: ""
    })
    const [commonItemDTOList, setcommonItemDTOList] = useState([])

    function mergeAndSort(data) {
        let mergedData = {};
    
        data.forEach(item => {
            if (!mergedData[item.week]) {
                mergedData[item.week] = { week: item.week, number: { avg: 0 } };
            }
            mergedData[item.week].number.avg += item.number.avg;
        });
    
        return Object.values(mergedData).sort((a, b) => a.week - b.week);
    }
    function mergeAndSortMonth(data) {
        let mergedData = {};
    
        data.forEach(item => {
            if (!mergedData[item.month]) {
                mergedData[item.month] = { month: item.month, number: { avg: 0 } };
            }
            mergedData[item.month].number.avg += item.number.avg;
        });
    
        return Object.values(mergedData).sort((a, b) => a.month - b.month);
    }
    function dayOfWeekFromDayNumber(dayNumber) {
        const daysOfWeek = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"];
        return daysOfWeek[dayNumber];
    }

    const init = () => {
        setLoading(true)
        debounce(() => {
            let api
            let color
            if (activeTab1 == "Sedentary") {
                api = activitySendatry
                color = "#FFCB33"
            } else if (activeTab1 == "Bathroom Activity") {
                api = bathroomUse
                color = "#4285F4"
            } else if (activeTab1 == "Activity") {
                api = activityActivity
                color = "#F63"
            } else if (activeTab1 == "Out of Bed") {
                api = activityoutOfBed
                color = "#34CECE"
            } else if (activeTab1 == "Night Activity") {
                api = activitynightActivity
                color = "#252F67"
            }
            console.log("query", query)
            api({ ...query, ...Dailyreport })
                .then(({ data }) => {
                    setLoading(false)
                    let dateType = query.dateType.toLowerCase()
                    if (dateType == "day") {
                        let info = data.timeLineInfo ? Object.values(data.timeLineInfo)[0] : null
                        console.log("info", info, data.timeLineInfo)
                        let row = info
                     
                        let servedata = row?.flatMap(e => {
                            return [
                                { value: [`2022-01-01 ${dayjs(e.reportStartTime).format('HH:mm:ss')}`, 900] },
                                { value: [`2022-01-01 ${dayjs(e.reportEndTime).format('HH:mm:ss')}`, 900] },
                                { value: [null, null] },
                            ]
                        })

                        setCharData({ seriesdata: servedata, color })
                    } else if (dateType == "week") {
                        let bioDailyReportsWeekComDTO = data.bioDailyReportsWeekComDTO
                        bioDailyReportsWeekComDTO = mergeAndSort(bioDailyReportsWeekComDTO);
                        let xdata = []
                        let values = []
                        bioDailyReportsWeekComDTO.forEach(e => {
                            xdata = [...xdata, dayOfWeekFromDayNumber(e.week-1)]
                            values = [...values, e.number.avg]
                        })
                        setCharData({
                            seriesdata: {
                                xdata,
                                values
                            }, color
                        })

                    } else {
                        const date = new Date();
                        const curMonth = (date.getMonth() + 1).toString().padStart(2, '0');
                        let bioDailyReportsMonthComDTO = data.bioDailyReportsMonthComDTO || []
                        bioDailyReportsMonthComDTO = mergeAndSortMonth(bioDailyReportsMonthComDTO);
                        let xdata = []
                        let values = []
                        bioDailyReportsMonthComDTO?.forEach(e => {
                            xdata = [...xdata, curMonth + '-'+ e.month || '']
                            values = [...values, e.number.avg]
                        })
                        console.log(xdata)
                        setCharData({
                            seriesdata: {
                                xdata,
                                values
                            }, color
                        })
                    }



                    let commonItemDTOList = data.commonItemDTOList || []

                    commonItemDTOList = commonItemDTOList?.map(e => {
                        return {
                            reportTypeName: e.itemName,
                            targetValue: e.target.replace("Hours", ""),
                            minutesCountStr: formatDuration(e.value),
                            valueStatu: e.valueStatu,
                            trendStatu: e.trendStatu,
                            trend: e.trend,
                            target: e.target,
                            priority: e.priority,
                            src: e.priority == 2 ? toiletGreen : toiletError
                        }
                    })
                    setcommonItemDTOList(commonItemDTOList)
                })
        })
    }

    useEffect(() => {

        init()
    }, [activeTab1, query])


    let hideColumns= ['target','trend','type']

    if(activeTab1 === "Activity" || activeTab1 === "Out of Bed") {
        hideColumns =  ['target','trend','type']
    }

    if(!["Day",'Daily'].includes(activeTab) ) {
        hideColumns=['target','trend','type']
    }

    return (
        <Spin spinning={loading} style={{marginTop: '200px'}}>
            <div className='pt-[50px]'>
                <div className=' pb-[10px] flex justify-between'>
                    <div className='text-[20px] text-cblock font-semibold'></div>
                    {/* {
                        IsShow && <div className='text-[14px] text-cblock font-semibold h-[40px] rounded-[20px] flex items-center pl-[20px] pr-[20px]' onClick={() => setIsShow(true)} style={{ 'border': ' 1px solid #333951' }} >Summary</div>
                    }
                     */}
                    {setIsShow && <SummaryBtn onClick={() => setIsShow(true)} />}
                </div>
                <div className='bg-lightwhite'>
                    <div className='bg-lightwhite pl-[20px] pt-[40px] pr-[20px]'>
                        <div className=''>
                            <TabMenus menus={menus1}
                                activeTab={activeTab1}
                                setActiveTab={setActiveTab1} />
                        </div>
                    </div>
                    <div style={{ opacity: loading?0:1}}>
                        {
                            query.dateType.toLowerCase() == "day" && <ActiveChar charData={charData} activeTab1={activeTab1} />
                        }
                        {
                            query.dateType.toLowerCase() == "week" && <ActivWeekChart charData={charData} activeTab1={activeTab1} />
                        }
                        {
                            query.dateType.toLowerCase() == "month" && <ActivWeekChart charData={charData} />
                        }

                        <HisTable data={commonItemDTOList} hideColumns={hideColumns} />
                    </div>
                </div>
            </div>
        </Spin>
    )
}

export default WeeklyCom