import React, { useEffect, useRef, useState } from "react";
import { Space, Table, Tag } from "antd";
import Card from "./Card";
import TrendsModel from "./TrendsModel";
import SosCall from "./SosCall";
import { activeTrend } from "@/api/detailOfElderly";
import TrendCom from "./TrendCom";
import { useCountStore } from "@/store/index";
import dayjs from "dayjs";

const Trends = ({ activeTab }) => {
  const Dailyreport = useCountStore((state) => state.Dailyreport);
  const [trendMoudle, setTrendMoudle] = useState(false);
  const [callMoudle, setCallMoudle] = useState(false);
  const [data, setData] = useState([]);
  const [type, setType] = useState(undefined);

  const handleChange = (row, index) => {
    setCallMoudle(true);
  };
  const init = () => {
    if (activeTab == "Trends") {
      activeTrend({
        ...Dailyreport,
        startTime: dayjs().format("YYYY-MM-DD"),
        endTime: dayjs().format("YYYY-MM-DD"),
      }).then((datas) => {
        let { data, code } = datas;
        if (code == "00000") {
          data = data.map((item, key) => {
            return item;
          });
          setData(data);
        }
      });
    }
  };
  useEffect(() => {
    init();
  }, [activeTab]);
  return (
    <>
      <TrendCom
        setType={setType}
        data={[
          {
            priorityColor: "#FA4B5E",
            functionId: "1",
            alertName: "Alert 1",
            count: 10,
          },
          {
            priorityColor: "#FA4B5E",
            functionId: "2",
            alertName: "Alert 2",
            count: 0,
          },
        ]}
        setTrendMoudle={setTrendMoudle}
        setCallMoudle={setCallMoudle}
      />

      <TrendsModel
        handleChange={(row) => handleChange(row)}
        iShow={trendMoudle}
        handleIsShow={setTrendMoudle}
      />
      <SosCall
        type={type}
        data={data}
        iShow={callMoudle}
        setIShow={setCallMoudle}
      />
    </>
  );
};

export default Trends;
