import React, { useEffect, useState } from 'react';
import ReactEcharts from 'echarts-for-react';
import 'echarts/lib/chart/bar';
import dayjs from 'dayjs'
import moon from '@/assets/icon/moon.svg'


function WeekSosChar({ charData }) {

    const [option, setOption] = useState({



    })
    const init = () => {
        // const colorList = [
        //     '#4f81bd',
        //     '#c0504d',
        //     '#9bbb59',
        //     '#604a7b',
        //     '#948a54',
        //     '#e46c0b'
        // ];
        // const data = [
        //     [5, 0, ],
        //     [1, 15],

        // ].map(function (item, index) {
        //     return {
        //         value: item,
        //         itemStyle: {
        //             color: colorList[index]
        //         }
        //     };
        // });
        // console.log("Data",data)

        if (charData?.seriesdata?.xdata?.length > 0) {
            const { values } = charData.seriesdata

            setOption({
                backgroundColor: 'rgba(0,0,0,0)',
                grid: {
                    left: '0',
                    right: '4%',
                    bottom: '3%',
                    top: '12%',
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    // data: xdata,
                    splitLine: {
                        show: false // 
                    },

                    axisTick: { show: false },

                    // boundaryGap: false // Y
                },
                yAxis: {
                    show: true,
                    name: 'bpm', // 
                    nameLocation: 'end', // 'end' 
                    nameTextStyle: { // 
                        color: '#333',
                        fontSize: 12,
                        padding: [0, 0, 10, -10] // 
                    },
                    splitLine: { show: false },
                    axisLine: { show: true },
                    type: 'value',
                    axisLabel: {
                        color: "#000",
                    },

                },
                series: [
                    {
                        type: 'custom',
                        renderItem: function (params, api) {
                            var yValue = api.value(1);
                            var start = api.coord([api.value(0), yValue]);
                            var size = api.size([api.value(1) - api.value(0), yValue]);
                            var style = api.style();
                            var barWidth = 20; // 20
                            var borderRadius = 10; // 
                            console.log('yValue', yValue)
                            if (yValue === 0) {
                                return {
                                    type: 'group',
                                    children: [{
                                        type: 'circle', // 
                                        shape: {
                                            cx: start[0], // x
                                            cy: start[1] - 10, // y
                                            r: 10 // 
                                        },
                                        style: style
                                    }]
                                };
                            }
                            return {
                                type: 'rect',
                                shape: {
                                    x: start[0] - barWidth / 2,
                                    y: start[1],
                                    width: barWidth,
                                    height: size[1],
                                    r: borderRadius // 
                                },
                                style: style
                            };
                        },

                        dimensions: ['x', 'y'],
                        encode: {
                            x: 'x',
                            y: ["y"],
                        },
                        data: values
                    }

                ],

            })

        } else {

            setOption({
                title: {
                    text: "There is currently no data available",
                    show: true,
                    left: "center",
                    top: "center",
                    textStyle: {
                        color: "#000",
                        fontSize: "20px",
                        fontWeight: 400,
                    },
                },
            })

        }

    }


    useEffect(() => {
        init()
    }, [charData])



    return <ReactEcharts option={option} notMerge={true} />;
}

export default WeekSosChar;