import React, { useEffect, useRef, useState } from 'react';
import Card from './Card'


export default function TrendCom({ data, setTrendMoudle, setCallMoudle, setType }) {
    const handleChange = (row, index) => {
        console.log("rpw", row)
        if (setCallMoudle) {
            setType(row.functionId)
            let t = setTimeout(() => {
                setCallMoudle(true)
                clearTimeout(t)
            }, 0);

        }
    }
    return <>
        <div>
            <div className='pt-[10px] pb-[10px] flex justify-between'>
                <div className='text-[20px] text-cblock font-semibold'>Trends</div>
                {
                    setTrendMoudle && <div onClick={() => setTrendMoudle(true)} className='cursor-pointer text-[14px] text-cblock font-semibold h-[40px] rounded-[20px] flex items-center pl-[20px] pr-[20px]' style={{ 'border': ' 1px solid #333951' }} >Summary</div>
                }

            </div>
            <div className='w-full'>
                <div className="h-full w-full grid grid-cols-8 gap-4">
                    {
                        data.map((item, key) => {
                            return (
                                // <div onClick={() => { handleChange(item) }} >
                                <div>
                                    <Card item={item} key={key} />
                                </div>
                            )
                        })
                    }

                </div>
                <div>
                </div>
            </div>
        </div>
    </>
}