
import React, { useEffect, useRef, useState } from 'react';
import { Table, Spin } from 'antd'
import { closedActive } from '@/api/detailOfElderly'
import { alertHistory } from '@/api/AdminSupportAgents'
import { useCountStore } from '@/store/index';
import dayjs from 'dayjs';
import AlertCom from "@/Components/AlertCom";

import Timer from '@/Components/svg/Timer'
const AlertsHistory = ({ activeTab, path }) => {
    const [data, setData] = useState([])
    const [loading, setLoading] = useState(true)
    const Dailyreport = useCountStore(state => state.Dailyreport);
    const [params, setParams] = useState({
        startTime: dayjs().subtract(10, 'day').format('YYYY-MM-DD'),
        endTime: dayjs().format('YYYY-MM-DD'),
    })
    const columns = [
        {
            title: 'Alert Date and Time',
            dataIndex: 'alarmStartTime',
            key: 'alarmStartTime',
            render: (text) => <div className='flex items-center text-14px text-cblock font-[500]'><Timer /><div className='ml-[10px]'> {text
            }</div></div>,
        },
        {
            title: 'Alert',
            dataIndex: 'alertName',
            key: 'alertName',
            render: (text, row) => <div className='flex items-center text-14px text-cblock font-[500]'> <div className='w-[26px] h-[26px] rounded-full  flex  justify-center items-center '> <AlertCom priority={row.priority} value={row.functionId} /></div> <div className='ml-[10px]'> {text}</div></div>,
        },
        // {
        //     title: 'Closed by',
        //     dataIndex: 'doneUserMap',
        //     key: 'doneUserMap',
        //     render: (text, row) => <div className='flex items-center flex items-center text-14px text-cblock font-[500]'>

        //         <div className='ml-[10px]'> {row.doneUserMap ? `${row.doneUserMap.role}(${row.doneUserMap.firstName}${row.doneUserMap.lastName})` : ""}</div></div>,
        // },
        {
            title: 'Alert Message ',
            dataIndex: 'alertMessage',
            key: 'alertMessage',
            render: (text, row) => <div className='flex items-center text-14px text-cblock font-[500]'> {text}</div>,
        },
        {
            title: 'Closing Time ',
            dataIndex: 'doneTime',
            key: 'doneTime',
            render: (text, row) => <div className='flex items-center text-14px text-cblock font-[500]'> {text || '-'}</div>,
        },
        // {
        //     title: 'Alert Life Time ',
        //     dataIndex: 'alertLife',
        //     key: 'alertLife',
        //     render: (text, row) => <div className='flex items-center text-14px text-cblock font-[500]'> {text}min</div>,
        // },
        {
            title: 'Comment',
            dataIndex: 'comment',
            key: 'comment',
            render: (text, row) => <div className='flex items-center text-14px text-cblock font-[500]'> {text || '-'}</div>,
        },

    ];
    const init = () => {
        alertHistory({ ...params, ...Dailyreport }).then(res=>{
            setData(res.data)
            setLoading(false)
        })
    }
    useEffect(() => {
        init()
    }, [activeTab])
    return (
        <>
        <Spin spinning={loading}>
            <div className='pt-[20px] pb-[10px] flex justify-between'>
                <div className='text-[20px] text-cblock font-semibold'>Alerts History</div>

            </div>
            <div className='min-h-[383px] bg-lightwhite'>
                <Table pagination={false} columns={columns} dataSource={data} className='newTable' />
            </div>
            </Spin>
        </>
    )
}
export default AlertsHistory