import ComCard from "@/Components/ComCard";
export default function HisCards({ data, typeName }) {
  function randomHexColor() { //
    var hex = Math.floor(Math.random() * 16777216).toString(16); //ffffff16
    while (hex.length < 6) { //whilehex606
     hex = '0' + hex;
    }
    return '#' + hex; //#'16
   }
  return (
    <div className="flex">
      {data?.map((e, key) => {
        return (
          <ComCard
            status={e.valueStatu}
            priority={e.priority}
            minutesTimerCount={e.minutesCountStr || e.minutesTimerCount || e.minutesCount}
            reportTypeName={typeName ? (e.reportTypeName.split(' ')[0] + " " + typeName) : e.reportTypeName}
            // bgColor={e.color || (e.reportType===8?'#707097':e.reportType===9?'#1d9b70':null)}
            // bgColor={"#ededed"}
          />
        );
      })}
    </div>
  );
}
