
import BotvITal from './BotvITal'
import BotWeekMonthCom from './BotWeekMonthCom'
import HistableCom from './HistableCom.jsx'

export default function BotvICom({ chatData,activeTab ,query,SetIsShow=false}) {
    // let formartData = chatData
    // if(chatData?.heartInfo) {
    //     let topList = chatData?.heartInfo?.topList || []
    //     if(activeTab=== 'Week') topList.sort((a, b) => a.week - b.week);
    //     else if(activeTab=== 'Month') topList.sort((a, b) => a.month - b.month);
    //     formartData = {
    //         ...chatData,
    //         heartInfo: {
    //             topList: topList
    //         }
    //     }
    //     console.log(chatData)
    //     chatData = formartData
    // }
    return (
        <>
            {
                activeTab == "Day" && <BotvITal chatData={chatData} SetIsShow={SetIsShow} />
            }

            {
                (activeTab == "Week") && <>
                    <BotWeekMonthCom data={chatData} isCard={true} SetIsShow={SetIsShow} status={1} activeTab={activeTab} title="Heart Rate Weekly Summary" typeName="Heart Rate" />
                    <BotWeekMonthCom data={chatData} isCard={true} SetIsShow={SetIsShow} status={2} activeTab={activeTab} title="Respiratory Rate Weekly Summary" typeName="Respiratory Rate" />
                </>
            }
            {
                (activeTab == "Month") && <>
                    <BotWeekMonthCom data={chatData} isCard={true} SetIsShow={SetIsShow} status={1} activeTab={activeTab} title="Heart Rate Month Summary" typeName="Heart Rate" />
                    <BotWeekMonthCom data={chatData} isCard={true} SetIsShow={SetIsShow} status={2} activeTab={activeTab} title="Respiratory Rate Month Summary" typeName="Respiratory Rate" />
                </>
            }
            {/* <HistableCom query={query} setOpen2={false} /> */}
        </>
    )

}