
import TimelIneChar from './TimelIneChar.jsx'
import SummaryBtn from '@/Components/SummaryBtn'
export default function reportCom({linData,setlineOpen2}){
    return (
        <>
          <div className='pt-[50px]'>
            <div className=' pb-[18px] flex justify-between'>
                <div className='text-[20px] text-cblock font-semibold'>Timeline</div>
                {
                    // setlineOpen2&&<div onClick={()=>setlineOpen2(true)}  className='cursor-pointer text-[14px] text-cblock font-semibold h-[40px] rounded-[20px] flex items-center pl-[20px] pr-[20px]' style={{ 'border': ' 1px solid #333951' }} >Summary</div>
                    setlineOpen2&&<SummaryBtn onClick={()=>setlineOpen2(true)} />
               }
                
            </div>
            <div style={{height:"400px"}}>
               <TimelIneChar data={linData}/>
            </div>
            </div>
        </>
    )
}