
import { Table } from "antd";
import Target from '@/Components/Target';
import StatusChar from '@/Components/StatusChar';
import QiuChar from '@/Components/QiuChar'
import AlertCom from "@/Components/AlertCom";
import { formatDuration } from '@/utils/seenYouUtil'

import TypeIcon from '@/Components/TypeIcon'
import Timer from '@/Components/svg/Timer'
export default function HistoryTable({ data, datalong, status,isShow,hideColumns = [] }) {
    console.log(isShow)
    console.log(datalong)
    const historyClome = [
        {
            title: 'Time',
            dataIndex: 'time',
            key: 'time',
            render: (text, row) => <div className='flex items-center text-14px text-cblock font-[500]'>
                <Timer /><div className='ml-[10px]'>
                    {/* {status == "Overview" ?row.shortTimeWeb : text} */}
                    {status == "Overview" ? row.alarmStartTime : text}
                </div></div>,
        },
        {
            title: 'Status',
            dataIndex: 'iconUrl',
            key: 'iconUrl',
            render: (text, row) => <div className='flex items-center text-14px text-cblock font-[500]'>
                {/* <div className='w-[26px] h-[26px] rounded-full  flex  justify-center items-center '>
                    <img className='w-full h-full' src={row.iconUrl} /></div> */}
               
                    <AlertCom priority={row.priority} value={row.functionId} />
                <div className='ml-[10px]'>     {status == "Overview" ? row.alertName : row.describe} </div>
            </div>,
        },
        {
            title: 'Target',
            dataIndex: 'target',
            key: 'target',
            render: (text) => <div className='flex items-center flex items-center text-14px text-cblock font-[500]'>
                <Target />
                <div className='ml-[10px]'> {formatDuration(text)}</div></div>,
        },
        // {
        //     title: 'Value',
        //     key: 'value',
        //     dataIndex: 'value',
        //     // render: (text, row) => <div className='flex items-center'>  <QiuChar status={row.priority == 0 ? 7 : row.priority == 1 ? 6 : 8} name={row.value} /></div>,
        //     render: (text, row) => <div className='flex items-center'>  <QiuChar status={row.priority == 0} name={row.value} /></div>,
        // },
        // {
        //     title: 'trend',
        //     key: 'trend',
        //     dataIndex: 'trend',
        //     render: (text, row) => <StatusChar name={text} status={row.trendValue} />

        // },
        {
            title: 'type',
            key: 'priority',
            dataIndex: 'priority',
            render: (text) => (
                <TypeIcon text={text} />

            )
        },

    ]

    return (
        <div className='min-h-[300px] bg-lightwhite'>
            {
                isShow ? 
                <Table
                    pagination={false}
                    columns={historyClome}
                    dataSource={datalong}
                    className={"newTable"}
                />:
                <Table
                    pagination={false}
                    columns={historyClome}
                    dataSource={data}
                    className={"newTable"}
                />
            }
            
        </div>
    )
}