import React, { useEffect, useState } from "react";
import ReactEcharts from "echarts-for-react";
import "echarts/lib/chart/bar";

function dayOfWeekFromDayNumber(dayNumber) {
  const daysOfWeek = [
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday",
    "Sunday",
  ];
  return daysOfWeek[dayNumber];
}

const currentDate = new Date();
let currentMonth = currentDate.getMonth() + 1;
currentMonth = currentMonth < 10 ? "0" + currentMonth : currentMonth;

function MyChart({ activeTab, activeTab1, cheartInfo }) {
  console.log("charin", cheartInfo);

  const [option, setOption] = useState({});
  const init = () => {
    try {
      const {
        char: {
          xdata,
          maxArray,
          minArray,
          maxdata,
          mindata,
          allAvgNumber,
          color,
        },
      } = cheartInfo;
      let dates = [];
      if (activeTab === "Month") {
        xdata.forEach((item, ri) => {
          dates[ri] = currentMonth + "-" + (item < 10 ? "0" + item : item);
        });
      } else if (activeTab === "Week") {
        xdata.forEach((item, ri) => {
          dates[ri] = dayOfWeekFromDayNumber(item - 1);
        });
      }

      setOption({
        backgroundColor: "rgb(247, 247, 255)",

        grid: {
          left: "1.5%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: {
          type: "category",
          splitLine: { show: false },
          axisTick: { show: false },
          data: dates,
        },
        yAxis: {
          name: "bpm", // 
          nameLocation: "end", // 'end' 
          nameTextStyle: {
            // 
            color: "#333",
            fontSize: 12,
            padding: [0, 0, 10, -10], // 
          },
          splitLine: { show: false },
          axisLine: { show: true },
          type: "value",
          axisLabel: {
            color: "#000",
          },
        },
        series: [
          {
            name: "",
            type: "pictorialBar",
            symbolSize: [15, 10],
            symbolOffset: [0, -5], // 
            symbolPosition: "end",
            z: 12,
            label: {
              normal: {
                show: true,
                offset: [1, 0],
                position: "top",
                // "formatter": "{c}%"
                fontSize: 13,
                fontWeight: 400,
                color: color,
                lineHeight: 15,
                top: "10px",
                formatter: function (params) {
                  return `Max\n${params.value}`;
                },
              },
            },
            itemStyle: {
              borderColor: color, // 
              borderWidth: 18, // 
              borderType: "solid", // 
            },
            color: "#fff",
            data: maxArray,
          },

          {
            name: "",
            type: "pictorialBar",
            symbolSize: [15, 10],
            symbolOffset: [0, -18], // 
            symbolPosition: "end",
            z: 12,
            label: {
              normal: {
                show: true,
                offset: [1, 60],
                position: "top",
                // "formatter": "{c}%"
                fontSize: 13,
                fontWeight: 400,
                color: color,
                lineHeight: 15,
                top: "20px",
                formatter: function (params) {
                  return `Min\n${params.value}`;
                },
              },
            },
            itemStyle: {
              borderColor: color, // 
              borderWidth: 18, // 
              borderType: "solid", // 
            },
            color: "#fff",
            data: minArray,
          },
          {
            name: "Placeholder",
            type: "bar",
            barWidth: 20,
            stack: "Total",

            itemStyle: {
              borderColor: "transparent",
              color: "transparent",
            },
            emphasis: {
              itemStyle: {
                borderColor: "transparent",
                color: "transparent",
              },
            },
            data: mindata,
          },
          {
            name: "Life Cost",
            type: "bar",
            color: color,
            stack: "Total",
            barWidth: 20,
            itemStyle: {
              barBorderRadius: [10, 10, 10, 10], // 
            },
            label: {
              show: false,
              position: "inside",
            },
            data: maxdata,
            markLine: {
              symbol: "none",
              lineStyle: {
                type: "solid", // 
              },
              data: [{ value: 500, name: "", yAxis: allAvgNumber }],
            },
          },
        ],
      });
    } catch (error) {
      setOption({
        backgroundColor: "rgb(247, 247, 255)",
        title: {
          text: "There is currently no data available",
          show: true,
          left: "center",
          top: "center",
          textStyle: {
            color: "#000",
            fontSize: "20px",
            fontWeight: 400,
          },
        },
      });
    }
  };

  useEffect(() => {
    init();
  }, [cheartInfo]);

  return <ReactEcharts option={option} notMerge={true} />;
}

export default MyChart;
