import { useState, useEffect } from 'react'
import HisTable from './assembly/HisTable.jsx'
import HisCards from './assembly/HisCards.jsx'
import Heartrate from '@/assets/icon/sleep/Heartrate.svg'
import respiratoryrate from '@/assets/icon/sleep/respiratoryrate.svg'
import totalsleeptime from '@/assets/icon/sleep/totalsleeptime.svg'
import { targetFormat } from '@/utils/comFunction.js'
import SummaryBtn from '@/Components/SummaryBtn'
import { formatDuration } from '@/utils/seenYouUtil'
export default function SleepScoreCard({ card, isShow, setOpen,activeTab }) {
    const [cards, setCards] = useState([])
    const [tabs, setTabs] = useState([])
    let src = {
        "Total Sleep Time": totalsleeptime,
        "Avg. Respiratory Rate": respiratoryrate,
        "Avg. Heart Rate": Heartrate
    }
    useEffect(() => {
        let data = card
      
        let dataInfo = data?.sleepScoreDailyVO?.detailInfo || data?.sleepScoreWeekVO?.topInfo || data?.sleepScoreMonthVO?.topInfo
        console.log("dataInfo",dataInfo,data)
        let arr0 = []
        for (const key in dataInfo) {
            let element = dataInfo[key];
            if (key == 8) {
                element.reportTypeName = "Total Sleep Time"
                element.minutesCount = formatDuration(Number(element.minutesCount))
            } else if (key == 6) {
                element.reportTypeName = "Avg. Respiratory Rate"
                element.minutesCount = element.minutesCount + element.unit
            } else if (key == 7) {
                element.reportTypeName = "Avg. Heart Rate"
                element.minutesCount = element.minutesCount + element.unit
            } else if (key == 9) {
                element.minutesCount = `${element.minutesCount}/100`
            }

            arr0.push(element)

        }
        
        let arr = JSON.parse(JSON.stringify(arr0))
        let arr1 = JSON.parse(JSON.stringify(arr0))
        arr = arr.map(e => {
            if (e.trendStatu == 1) {
                e.valueStatu = 6
            }
            if (e.trendStatu == 2) {
                e.valueStatu = 7
            }
            if (e.trendStatu == 3) {
                e.valueStatu = 8
            }

            const {priority} = e;
            let bgColor =  "#4285F4"

            if(priority === 0) {
                bgColor = "#6FCF97"
            } else if(priority === 1) {
                bgColor = "#FAB515"
            } else if(priority === 2) {
                bgColor = "#FA4B5E"
            }  

            e.bgColor=bgColor
            return e
        })
        console.log("Arr",arr)
        setTabs([...arr1.filter(e => {
            // if (e.reportTypeName !== "sleep score") {
                if (e.reportTypeName)
                    e.src = src[e.reportTypeName]
                e.color = undefined
                e.targetValue = targetFormat(e)
                return e
            // }
        })])

        setCards(arr)
    }, [card])



    let hideColumns= []

    hideColumns=['target','trend','type']
    // if(!["Day",'Daily'].includes(activeTab) ) {
    //     hideColumns=['target','trend','type']
    // }

    return (
        <>
            <div className='pt-[10px] pb-[10px] flex justify-between'>
                <div className='text-[20px] text-cblock font-semibold'>Sleep Score</div>
                {isShow && <SummaryBtn onClick={() => setOpen(true)} />}


            </div>
            <div className='pt-[10px] flex '>
                <HisCards data={cards} />
            </div>
            <div>
                <HisTable data={tabs} hideColumns={hideColumns} />
            </div>
        </>
    )
}