import React, { useState, useEffect } from 'react';
import ReactEcharts from 'echarts-for-react';
import 'echarts/lib/chart/bar';

function MyChart({ proSeries }) {
    const [option, setOption] = useState({})
    useEffect(() => {
        try {
            let xdata = proSeries.map(e => {
                return e.name
            })
            setOption({
                title: {
                    text: proSeries.length === 0 ? "There is currently no data available" : '',
                    show: true,
                    left: "center",
                    top: "center",
                    textStyle: {
                        color: "#aaa",
                        fontSize: "20px",
                        fontWeight: 400,
                    },
                },
                backgroundColor: '#F7F7FF',
                grid: {
                    left: "5%",
                    right: "5%",
                    top: 50,
                    bottom: "7%"
                },
                yAxis: {
                    name: 'Minutes', // 
                    nameLocation: 'end', // 'end' 
                    nameTextStyle: { // 
                        color: '#333',
                        fontSize: 12,
                        padding: [0, 0, 10, -10] // 
                    },
                    splitLine: { show: false },
                    axisLine: { show: true },
                    type: 'value',
                    axisLabel: {
                        color: "#000",
                    }
                },
                xAxis: {
                    type: 'category',
                    // data: activeTab === 'Week' ? ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'] : xdata,
                    data: xdata,
                    splitLine: {
                        show: false 
                    },
                  
                },
                series: proSeries
            })
        } catch (error) {}
    }, [proSeries])






    return <div><ReactEcharts notMerge={true} option={option} /></div>;
}

export default MyChart;