import React, { useEffect, useRef, useState } from 'react';
import { Spin } from 'antd';
import { activeTimeLine, listByElder } from '@/api/detailOfElderly'
import { alertHistory } from '@/api/AdminSupportAgents'

import dayjs from 'dayjs';
import OverviewModel from './OverviewModel'
import SleepHistorySummary from './SleepHistorySummary.jsx'
import ReportCom from './reportCom.jsx'
import toast from "react-hot-toast";
import GreenToast from "@/Shared/Tosat/GreenToast";
import SleepLineSummary from './SleepLineSummary.jsx'
import HistableCom from './HistableCom.jsx'
import { useCountStore } from '@/store/index';
import ls from 'store2'

const Overview = ({ activeTab, path, Config }) => {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [isShow, setIsShow] = useState(false);
    const [open2, setOpen2] = useState(false);
    const [data, setData] = useState([])
    const [config, setConfig] = useState({})
    const [alertData, setAlertData] = useState({})
    const [linData, setLinData] = useState([])
    const [lineOpen2, setlineOpen2] = useState(false);
    const [loading, setLoading] = useState(true)
    const Dailyreport = useCountStore(state => state.Dailyreport);
    const [params, setParams] = useState({
        startTime: dayjs().subtract(6, 'day').format('YYYY-MM-DD'),
        endTime: dayjs().format('YYYY-MM-DD'),
        "type": 0
    })

    const overviewInit = async () => {
        console.log(Config);
        
        let elderRoomRadarId = undefined
        let radarId = []
        let roomNumber = undefined
        let demo = Config.demo

        if (Config.elderCommonVO.length > 0) {
            elderRoomRadarId = Config.elderCommonVO[0].radarRoomId
            roomNumber = Config.elderCommonVO[0].roomNumber
        }
        if (Config.elderRoomRadarDTOList?.length > 0) {
            // elderRoomRadarId=Config.elderRoomRadarDTOList[0].id
            radarId = Config.elderRoomRadarDTOList.map(e => e.radarId)
        }
        // activeTimeLine({ ...{demo,elderRoomRadarId,radarId,roomNumber} })
        let row = await Promise.all([listByElder({ activeId: path.id }), activeTimeLine({ ...{ demo, elderRoomRadarId, radarId, roomNumber, ...Dailyreport } })])
        console.log("configrow", row)
        let data = row[0].data
        console.log(data)
        if (data.length > 0) {
            setConfig({ ...data[0] })
        }
        setData(data)

        setLinData(row[1].data)
        setLoading(false)

    }
    const handleAlertChange = () => {
        if (alertData.id) {
            setIsModalOpen(true)
        } else {
            toast.custom((t) => (
                <GreenToast
                    t={t}
                    text={"There is currently no police protection"}
                    title={"warning"}
                />
            ));
        }

    }
    const minutesFromNow = (dateString) => {
        // 
        const givenDate = dayjs(dateString, "YYYY-MM-DD hh:mm A");

        // 
        const now = dayjs();

        // 
        const differenceInMinutes = now.diff(givenDate, 'minute');

        return differenceInMinutes;
    }
    useEffect(() => {
        console.log('activeTab', activeTab)
        if (activeTab == "Overview") {
            overviewInit()
        }
    }, [activeTab])

    const intervalRef = useRef(null);
    useEffect(() => {
        // 
        const startTimer = () => {
          intervalRef.current = setInterval(() => {
            alertHistory({ ...params, ...Dailyreport }).then(res=>{
                let lists = res.data.filter(res=>res.type !== 1)
                let item = lists[0]
                setAlertData(item)
                console.log(item)
                setLoading(false)
            })
          }, 2000); // 1
        };
    
        // 
        startTimer();
    
        // 
        return () => {
          if (intervalRef.current) {
            clearInterval(intervalRef.current);
            console.log('Timer stopped');
          }
        };
    }, []); // 

    return (
        <Spin spinning={loading}>
            {/* <div className='pt-[50px] pb-[10px] text-[20px] text-cblock font-semibold'>Status</div> */}
            <div className='pt-[10px] ' style={{display: alertData?.type === 1 ? 'none' : 'block'}}>
                <div 
                    className='w-full bg-onBackWarring p-[20px]  rounded-[20px] flex items-center flex-col' 
                    style={{ 
                        display: alertData?.alertName ? 'flex' : 'none',
                        "boxShadow": "0px 0px 50px -10px rgba(0, 0, 0, 0.15)",
                        "backgroundColor": alertData?.priority === 1 ? '#fab515' : alertData?.priority === 2 ? '#f33e1b' : '#80caa7'
                    }} 
                >
                    <div className='w-[36px] h-[36px] rounded-[100px] bg-white flex justify-center items-center'>
                        <div dangerouslySetInnerHTML={{
                            __html: `<svg xmlns="http://www.w3.org/2000/svg" width="16" height="14" viewBox="0 0 16 14" fill="none">
                            <path d="M15.374 11.3978L9.35482 1.4154C9.11768 1.02214 8.6851 0.777832 8.22586 0.777832C7.76663 0.777832 7.33405 1.02214 7.09688 1.41542L1.0777 11.3978C0.832624 11.8043 0.825372 12.3135 1.05876 12.7268C1.2922 13.1401 1.73203 13.3968 2.20666 13.3968H14.2451C14.7197 13.3968 15.1596 13.1401 15.393 12.7268C15.6264 12.3134 15.6191 11.8042 15.374 11.3978ZM14.5611 12.257C14.4968 12.3707 14.3757 12.4414 14.2451 12.4414H2.20666C2.07599 12.4414 1.9549 12.3708 1.89067 12.257C1.82641 12.1432 1.82842 12.003 1.89586 11.8912L7.91509 1.90877C7.98038 1.80051 8.09947 1.73325 8.22589 1.73325C8.35229 1.73325 8.47137 1.80051 8.53666 1.90877L14.5558 11.8912C14.6233 12.003 14.6253 12.1432 14.5611 12.257Z" fill="#FA4B5E"/>
                            <path d="M8.23006 4.7085C7.86664 4.7085 7.58301 4.90351 7.58301 5.24921C7.58301 6.304 7.70708 7.8197 7.70708 8.87451C7.70711 9.14928 7.94646 9.2645 8.23009 9.2645C8.44281 9.2645 8.74418 9.14928 8.74418 8.87451C8.74418 7.81973 8.86825 6.30402 8.86825 5.24921C8.86825 4.90353 8.57577 4.7085 8.23006 4.7085Z" fill="#FA4B5E"/>
                            <path d="M8.23917 9.87622C7.84918 9.87622 7.55664 10.1864 7.55664 10.5588C7.55664 10.9222 7.84915 11.2413 8.23917 11.2413C8.60259 11.2413 8.91285 10.9222 8.91285 10.5588C8.91285 10.1864 8.60257 9.87622 8.23917 9.87622Z" fill="#FA4B5E"/>
                            </svg>` }}>

                        </div>
                    </div>
                    <div className='mt-[10px] text-white text-[20px] font-semibold '>
                        {alertData?.alertName}
                    </div>
                    <div className='mt-[10px] text-white text-[16px]'>
                        {alertData?.alertMessage}
                    </div>
                    {/* <div className='mt-[18.5px] text-[14px] text-white '>{minutesFromNow(alertData.yearTime + alertData.displayShortTime) || 0}  Minutes ago</div> */}
                    <div className='mt-[10px] text-[14px] text-white '>{alertData?.alarmStartTime}</div>
                    <div
                        onClick={() => handleAlertChange()}
                        className={`mt-[18.5px] text-[14px] relative re cursor-pointer h-[40px] mr-[10px] pl-[20px] pr-[20px]  rounded-[20px]   flex items-center text-white bg-cblock `}>
                        Mark as Completed
                    </div>
                </div>
            </div>

            <HistableCom  query={{}} activeTab="Day" itemTab={activeTab} open2={open2} setOpen2={setOpen2} isShow={false} status="Overview" />
            <ReportCom linData={linData} setlineOpen2={setlineOpen2} />
            <OverviewModel path={path} config={alertData} isModalOpen={isModalOpen} setIsModalOpen={setIsModalOpen} setLoading={setLoading} />
            <SleepHistorySummary
                title={"Overview History Summary"}
                data={data}
                status="Overview"
                isShow={open2}
                loading={loading}
                setIsShow={setOpen2} />
            <SleepLineSummary
                path={path}
                title={"Timeline History Summary"}
                isShow={lineOpen2}
                setIsShow={setlineOpen2}
            />
            <br/><br/>
        </Spin>
    )
}

export default Overview;