
import dayjs from 'dayjs'
export default function Timerline({ lines }) {
    let sortedReports = lines.sort((a, b) => a.reportStartTime - b.reportStartTime);
    sortedReports = sortedReports.filter(item => item.reportType !== 8);
    const totalValue = sortedReports.reduce((sum, item) => sum + item.value, 0);
    let lins = [], total = 0
    return (
        <>
        <div  style={{ borderBottom: "1px solid #E2E3E5" }}>
                    {
                        sortedReports.length > 0 && (
                            <>
                                <div className='w-full pl-[21px] pr-[21px] pt-[41px] pb-[35px]' style={{ backgroundColor: '#F7F7FF' }}>
                                    <div className='h-[67px] w-full flex'>
                                        {
                                            sortedReports.map(e => {
                                                return (
                                                    <div className='h-full' style={{ backgroundColor: e.color, width: (e.value * 100)/totalValue + "%" }}></div>
                                                    // <div className='h-full' style={{ backgroundColor: e.color, width: (e.value*100 / totalValue) + "%" }}></div>
// 
                                                )
                                            })
                                        }
                                    </div>
                                    <div className='mt-[22px] w-full flex'>
                                        {
                                            sortedReports.map((e,ri) => {
                                                const wd = e.proportion * 100
                                                lins.push(wd)
                                                total = total + wd
                                                const n10 = (total%20).toFixed(0)
                                                // console.log(wd,total,n10,ri,sortedReports.length,dayjs(e.reportStartTime).format("HH:mm"))
                                                return (
                                                    <div 
                                                        className='text-[12px] font-[500] text-cblock ' 
                                                        style={{ 
                                                            width: (e.value * 100)/totalValue + "%",
                                                            position:'relative',
                                                            left: '-12px',
                                                            opacity:(ri===0 || lins[ri-1]>6 || ri === sortedReports.length-1) ? 1 : 0
                                                        }} 
                                                    >{dayjs(e.reportStartTime).format("HH:mm")}</div>
                                                )
                                            })
                                        }
                                    </div>
                                </div>

                            </>
                        )
                    }
                </div>
        </>
    )
}