import request from "../utils/request";

export function bootElerProfilePage(data) {
  return request({
    url: `/boot/elder/profile/page`,
    method: "post",
    data,
  });
}
export function profileDetail(id) {
  return request({
    url: `/boot/elder/profile/detail?elderId=${id}`,
    method: "post",
  });
}

export function elderLists(data) {
  return request({
    url: `/boot/elder/profile/list`,
    method: "post",
    data,
  });
}

export function reportHistory(data) {
  return request({
    url: `/boot/report/history/list`,
    method: "post",
    data,
  });
}

export function OvreportHistory(data) {
  return request({
    url: `/boot/active/web/active/list/by/elder?elderId=${data.elderId}`,
    method: "post",
    data,
  });
}

export function NewOvreportHistory(data) {
  return request({
    url: `/boot/active/active/list/by/elder/summary?elderId=${data.elderId}&pop`,
    method: "post",
    data,
  });
}

export function alertHistory(data) {
  return request({
    url: `/boot/active/active/list/by/elder/summary?elderId=${data.elderId}&alertsHistory`,
    method: "post",
    data,
  });
}
