
import React, { useEffect, useRef, useState } from 'react';
import { LeftOutlined, RightOutlined } from '@ant-design/icons';
import { Space, Table, Tag } from 'antd';
import TabMenus from '@/Components/TabMenus'
import TimeSelection from '@/Components/TimeSelection'
import { activeTrend } from '@/api/detailOfElderly'
import { useCountStore } from '@/store/index';
import Card from './Card'
import dayjs from 'dayjs'
import './SleepScore.css';
import ModelTitle from '@/Components/ModelTitle/index'


const ActiveitiesModel = ({ iShow, handleIsShow, handleChange }) => {
    const Dailyreport = useCountStore(state => state.Dailyreport);
    const [List, setList] = useState([
        {
            text: "Day",
            bo: true,
            warring: false
        },

        {
            text: "Week",
            bo: false,
            warring: false
        },
        {
            text: "Month",
            bo: false,
            warring: false
        }
    ]);
    const [activeTab, setActiveTab] = useState('Day')
    const [query, setQuery] = useState({
     
        "startTime": dayjs().format('YYYY-MM-DD'),
        "endTime": ""
    })

    const [data, setData] = useState([])

    const handleTimerClick = (row) => {

        if (activeTab == "Day") {

            setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: row.value.format('YYYY-MM-DD') })
        } else {

            setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: row.endvalue ? row?.endvalue?.format('YYYY-MM-DD') : "" })
        }
    }
    const rest = () => {
        setQuery({
            "startTime":dayjs().format('YYYY-MM-DD'),
            "endTime": dayjs().format('YYYY-MM-DD'),
        })
        handleIsShow(false)
    }
    const init = () => {
        if (query.startTime == "") return
        activeTrend({
            ...Dailyreport,
            ...query
        })
            .then(datas => {
                let { data, code } = datas
                if (code == "00000") {
                  
                    setData(data)
                }


            })
    }
    useEffect(() => {
        if (iShow) {
            init()
        }
    }, [iShow, query])


    return (
        <>
            {
                iShow && <div className='mode-back'> <div className='sleepModel'>

                    <ModelTitle title={"Trends"} setIsShow={rest} />
                    <div className='flex flex-col items-center'>

                        <div className='flex mt-[44px]'>
                            <TabMenus menus={List} activeTab={activeTab} setActiveTab={setActiveTab} />

                        </div>
                        <div className='mt-[45px]'>
                            <TimeSelection name={activeTab} handleClick={handleTimerClick} />
                        </div>
                        
                    </div>
                    <div className='pt-[65px] pb-[10px] flex justify-between'>
                        <div className='w-full'>
                            <div className="h-full w-full grid grid-cols-8 gap-4">
                                {
                                    data.map((item, key) => {
                                        return (
                                            // <div onClick={() => { handleChange(item) }} >
                                            <div>
                                                <Card item={item} key={key} />
                                            </div>
                                        )
                                    })
                                }

                            </div>
                            <div>
                            </div>
                        </div>
                    </div>
                </div>
                </div>
            }
        </>
    );
};

export default ActiveitiesModel;
