
import React, { useEffect, useRef, useState } from 'react';
import { Spin } from 'antd';
import ModelTitle from '@/Components/ModelTitle/index'
import TabMenus from '@/Components/TabMenus'
import TimeSelection from '@/Components/TimeSelection'
import './SleepScore.css';
import HistableCom from './HistableCom.jsx'

const SleepHistorySummary = ({ data, title, isShow, setIsShow,status  }) => {
    const [loading, setLoading] = useState(true)

    const [List, setList] = useState([
        {
            text: "Day",
            bo: true,
            warring: false
        },

        {
            text: "Week",
            bo: false,
            warring: false
        },
        {
            text: "Month",
            bo: false,
            warring: false
        }
    ]);

    const [activeTab, setActiveTab] = useState('Day')
    const [query, setQuery] = useState({})
    const handleTimerClick = (row) => {
        setLoading(true)
        setTimeout(() => {
            setLoading(false)
        }, 4000);
        const rowDay = row.value.format('YYYY-MM-DD')
        if (activeTab == "Day") {
            setQuery({ ...query, startTime: rowDay, endTime: rowDay })
        } else {
            setQuery({ ...query, startTime: rowDay, endTime: row.endvalue ? row?.endvalue?.format('YYYY-MM-DD') : "" })
        }
    }
    useEffect(()=>{
        if(isShow){
            setActiveTab("Day")
        }
    },[isShow])
 

    return (
        <>{isShow &&
            <div className='mode-back'>
                <div className='sleepModel'>
                    <Spin spinning={loading}>
                    <ModelTitle title={title} setIsShow={setIsShow} />
                    <div className='flex flex-col items-center'>
                        <div className='flex mt-[44px]'>
                            <TabMenus menus={List} activeTab={activeTab} setActiveTab={setActiveTab} />
                        </div>
                        <div className='mt-[45px]'>
                            <TimeSelection name={activeTab} handleClick={handleTimerClick} />
                        </div>
                    </div>
                    
                    <HistableCom activeTab={activeTab} status={status}  isShow={isShow} query={query} setOpen2={false}/>
                    </Spin>
                </div>
            </div>
        }</>
    );
};

export default SleepHistorySummary;
