
import React, { useEffect, useRef, useState } from 'react';
import { LeftOutlined, RightOutlined } from '@ant-design/icons';
import { Space, Table, Tag } from 'antd';
import TabMenus from '@/Components/TabMenus'
import TimeSelection from '@/Components/TimeSelection'
import SosChar from './SosChar'
import WeekSosChar from './WeekSosChar'
import './SleepScore.css';
import HeadSculpture from '@/Components/HeadSculpture/index.jsx';
import ModelTitle from '@/Components/ModelTitle/index'
import { reportChatAlert } from '@/api/detailOfElderly'
import dayjs from 'dayjs'
import QiuChar from '@/Components/QiuChar'
import HistableCom from './HistableCom.jsx'
import { useCountStore } from '@/store/index';
const SosCallModel = ({ iShow, setIShow, data,type }) => {
    const Dailyreport = useCountStore(state => state.Dailyreport);
    const [List, setList] = useState([

        {
            text: "Day",
            bo: true,
            warring: false
        },
        {
            text: "Week",
            bo: true,
            warring: false

        },
        {
            text: "Month",
            bo: true,
            warring: false
        }

    ]);
    const [charData, setCharData] = useState({
        seriesdata: [],
        color: ""
    })
    const [activeTab, setActiveTab] = useState('Day')
    const [query, setQuery] = useState({})


    const handleTimerClick = (row) => {
        console.log("row", row)
        if (activeTab == "Day") {

            setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: "" })
        } else {

            setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: row.endvalue ? row?.endvalue?.format('YYYY-MM-DD') : "" })
        }
    }
    const init = () => {
        if (query.startTime == "") return
       console.log("type",type)
        reportChatAlert({
            ...query,...Dailyreport,type
        })
            .then(datas => {
                let { data, code } = datas
                let dateType = activeTab.toLowerCase()
                if (dateType == "day") {
                    let row = data.timeLineInfo["1"]
                    let servedata = row.flatMap(e => {
                        return [
                            { value: [`2022-01-01 ${dayjs(e.reportStartTime).format('HH:mm:ss')}`, 900] },
                            { value: [`2022-01-01 ${dayjs(e.reportEndTime).format('HH:mm:ss')}`, 900] },
                            { value: [null, null] },
                        ]
                    })

                    setCharData({ seriesdata: servedata, color: "#FA4B5E" })
                } else {
                    let comtoto = data.bioDailyReportsMonthComDTO || data.bioDailyReportsWeekComDTO
                    let xdata = []
                    let values = []
                    comtoto.forEach(e => {
                        xdata = [...xdata, e.month || e.week]
                        values = [...values, e.number.avg]
                    })
                    console.log('xdata', xdata)
                    console.log('values', values)
                    values = values.map((e, index) => {
                        let color = "#FA4B5E"
                        let vlaue = e


                        if (vlaue == 0) {
                            color = "#989ba7"
                        }

                        return {
                            value: [xdata[index], vlaue],
                            itemStyle: {
                                color
                            }
                        }
                    })

                    setCharData({
                        seriesdata: {
                            xdata,
                            values
                        },
                    })
                }

            })
    }
    useEffect(() => {
        if (iShow) {
            init()
        }
    }, [iShow, query])
    return (
        <>
            {

                iShow && <div className='mode-back'><div className='sleepModel'>
                    <div className="flex">
                        <div className=" bg-white w-[483px]  h-[100vh] overflow-auto " style={{ "borderRight": "1px solid #E2E3E5", }}>
                            {
                                data?.map(e => {
                                    return (
                                        <>
                                            <div className='pl-[23px] pr-[23px] pt-[26px] pb-[26px] flex justify-between' style={{ background: e.priorityColor }}>
                                                <div className='flex items-center'>
                                                    {
                                                        e.iconUrl ? <img className='w-[32px] h-[32px] rounded-full' src={e.iconUrl} /> : <>
                                                            <HeadSculpture svg={` <svg xmlns="http://www.w3.org/2000/svg" width="19" height="16" viewBox="0 0 19 16" fill="none">
                                    <path d="M19 6.56738C19 7.87856 14.7448 8.9415 9.5 8.9415C4.25525 8.9415 0 7.87856 0 6.56738C0 5.95549 0.926574 5.39954 2.45105 4.97647C2.37413 5.27717 2.30769 5.55339 2.25175 5.79115C2.21962 5.93018 2.21576 6.07424 2.24038 6.21479C2.26501 6.35534 2.31762 6.48951 2.39511 6.60933C2.48845 6.74574 2.60788 6.86231 2.74651 6.95233C2.88514 7.04235 3.04021 7.10403 3.2028 7.13381C3.57692 7.20724 3.88462 7.27717 4.16434 7.3401C5.36364 7.60933 6.15385 7.78766 9.5 7.78766C12.9091 7.78766 13.6189 7.65479 14.9056 7.41353C15.1608 7.36458 15.4406 7.31213 15.7727 7.25619C15.9438 7.23018 16.1074 7.1681 16.2527 7.07407C16.398 6.98003 16.5216 6.85618 16.6154 6.71073C16.6961 6.58111 16.7479 6.4356 16.7672 6.28414C16.7865 6.13268 16.7729 5.97883 16.7273 5.83311C16.6329 5.51842 16.5525 5.23171 16.4755 4.95898C18.042 5.38205 19 5.94849 19 6.56738Z" fill="white"/>
                                    <path d="M12.5585 11.8793C11.8564 11.9684 11.4911 12.188 11.4557 12.3157C11.4497 12.3372 11.433 12.4598 11.6934 12.7418C11.8064 12.8548 11.9412 12.9438 12.0896 13.0033C12.2379 13.0629 12.3968 13.0918 12.5566 13.0884C13.0321 13.0929 13.491 12.9132 13.8369 12.5868C14.198 12.2269 14.2073 12.0954 14.2055 12.0726C14.1305 11.9466 13.4445 11.7666 12.5585 11.8793Z" fill="white"/>
                                    <path d="M6.63888 11.8793C6.45565 11.8558 6.27113 11.8439 6.0864 11.8438C5.47981 11.8438 5.0494 11.9766 4.98828 12.0824C4.98965 12.0954 4.99904 12.2272 5.36013 12.5864C5.70393 12.9161 6.16414 13.0965 6.64041 13.0884C6.8002 13.0918 6.95903 13.0629 7.10737 13.0034C7.25571 12.9439 7.39049 12.855 7.50361 12.7421C7.76414 12.4597 7.74741 12.3372 7.74144 12.3156C7.7061 12.1879 7.34091 11.9684 6.63888 11.8793Z" fill="white"/>
                                    <path d="M15.9134 9.89607C15.4152 9.98327 14.8958 10.0593 14.3564 10.1205C12.7432 10.2869 11.1221 10.3648 9.50037 10.354C7.87866 10.3648 6.25757 10.2869 4.64438 10.1205C4.10491 10.0593 3.58555 9.98327 3.08733 9.89607C2.41353 9.77816 2.05312 10.6065 2.18523 11.2695C2.2849 11.8493 2.49963 12.4032 2.81675 12.8987C3.13388 13.3942 3.54698 13.8212 4.03171 14.1546C5.07487 14.8396 6.30645 15.1804 7.55336 15.1293C7.74214 15.1153 7.92594 15.0622 8.09314 14.9734C8.26033 14.8846 8.40727 14.7621 8.52465 14.6135C8.84297 14.2864 8.99238 14.0419 9.50038 14.0338C10.0084 14.0419 10.1578 14.2864 10.4761 14.6135C10.5935 14.7621 10.7404 14.8846 10.9076 14.9734C11.0748 15.0622 11.2586 15.1153 11.4474 15.1293C12.6943 15.1804 13.9259 14.8396 14.969 14.1546C15.4538 13.8212 15.8669 13.3942 16.184 12.8987C16.5012 12.4033 16.7159 11.8493 16.8155 11.2695C16.9491 10.5989 16.5822 9.77903 15.9134 9.89607ZM8.0188 13.2161C7.84215 13.3986 7.6303 13.5433 7.39608 13.6414C7.16186 13.7395 6.91015 13.7891 6.65621 13.7871C6.63965 13.7878 6.62275 13.7878 6.60619 13.7878C5.95699 13.787 5.33354 13.5338 4.86767 13.0816C4.65341 12.8686 4.15129 12.3687 4.33005 11.8503C4.56514 11.1685 5.74418 11.1323 6.24288 11.1469C7.17061 11.176 8.22878 11.4495 8.41658 12.129C8.45082 12.3291 8.43213 12.5348 8.36235 12.7255C8.29257 12.9162 8.17413 13.0853 8.0188 13.2161ZM14.3309 13.0816C13.8651 13.5337 13.2416 13.7869 12.5924 13.7877C12.5757 13.7877 12.559 13.7877 12.5421 13.787C12.2881 13.789 12.0364 13.7394 11.8023 13.6412C11.5681 13.543 11.3563 13.3983 11.1796 13.2158C11.0243 13.085 10.9059 12.9159 10.8362 12.7253C10.7664 12.5346 10.7478 12.329 10.782 12.1289C10.9698 11.4494 12.028 11.1759 12.9557 11.1469C13.4534 11.1329 14.6331 11.1688 14.8684 11.8503C15.047 12.368 14.5452 12.8682 14.3309 13.0816Z" fill="white"/>
                                    <path d="M15.6535 6.5668C13.7059 6.89897 13.5416 7.08778 9.49961 7.08778C5.44017 7.08778 5.28632 6.83253 3.33877 6.44792C3.08003 6.39547 2.87723 6.20316 2.93317 5.94442C3.0066 5.62274 3.10452 5.22764 3.22688 4.78358C3.5101 3.73113 3.9052 2.39547 4.33178 1.08778C4.39795 0.858418 4.51777 0.648104 4.68132 0.474218C4.84487 0.300332 5.04746 0.167868 5.27233 0.0877761C5.80031 -0.118518 6.3038 0.14372 6.9122 0.364C7.73929 0.794806 8.66167 1.01007 9.59401 0.989874C11.122 0.937427 11.3842 0.405958 13.0066 0.0178461C13.2177 -0.0141342 13.4331 -0.00305 13.6398 0.0504311C13.8465 0.103912 14.0403 0.198683 14.2094 0.329035C14.9786 0.836028 15.0031 1.77659 15.3318 3.25561C15.4541 3.80106 15.566 4.26959 15.6989 4.76959C15.8038 5.15421 15.9192 5.55631 16.059 6.03183C16.0731 6.0942 16.0733 6.15889 16.0597 6.22136C16.0462 6.28383 16.0191 6.3426 15.9805 6.39354C15.9419 6.44448 15.8926 6.48638 15.8361 6.51631C15.7796 6.54623 15.7173 6.56346 15.6535 6.5668Z" fill="white"/>
                                    </svg>`} />
                                                        </>
                                                    }

                                                    <div className='pl-[16px] text-[14px]   font-medium '>{e.alertName}</div>
                                                </div>
                                                <div className='text-[20px] relative right-[26px] flex items-center'>
                                                    <div>{e.count}</div>
                                                    <div className='ml-[10px]'>
                                                        <QiuChar status={e?.priority} name={""} />
                                                    </div>
                                                </div>
                                            </div>
                                        </>
                                    )
                                })
                            }
                           

                        </div>
                        <div className="flex-grow bg-[#fff] h-[100vh] overflow-auto  pb-[38px] pl-[50px] pr-[50px]">
                          <div className='sticky top-0 bg-white z-50'>
                          <ModelTitle title={"SOS Calls"} setIsShow={setIShow} />
                          </div>
                       

                            <div className='flex flex-col items-center '>
                                {/* <div className='mt-[12px] text-[20px] text-cblock font-semibold '>Trends</div> */}
                                <div className='flex mt-[44px]'>
                                    <TabMenus menus={List} activeTab={activeTab} setActiveTab={setActiveTab} />

                                </div>
                                <div className='mt-[45px]'>
                                    <TimeSelection name={activeTab} handleClick={handleTimerClick} />
                                </div>

                            </div>
                            <div className='pt-[40px] pb-[10px] w-full h-[300px]'>
                                {
                                    activeTab == "Day" && <SosChar charData={charData} />
                                }
                                {
                                    (activeTab == "Week" || activeTab == "Month") && <WeekSosChar charData={charData} />
                                }

                            </div>
                            <div className=''>
                             <HistableCom  query={query} setOpen2={false}/>
                            </div>
                        </div>

                    </div>
                </div>
                </div>
            }
        </>
    );
};

export default SosCallModel;
