
import React, { useEffect, useRef, useState } from 'react';
import './SleepScore.css';
import { getBioVtalSummary, getReportSummary } from '@/api/detailOfElderly'
import TabMenus from '@/Components/TabMenus/index'
import TimeSelection from '@/Components/TimeSelection/index'
import BotvICom from './BotvICom'
import ModelTitle from '@/Components/ModelTitle/index'
import BotvITal from './BotvITal'
import BotWeekMonthCom from './BotWeekMonthCom'
import HistableCom from './HistableCom.jsx'
import { useCountStore } from '@/store/index';
import { Spin, Table, Tag } from 'antd';

const BioVitalSummary = (props) => {
    const Dailyreport = useCountStore(state => state.Dailyreport);
    const { SetIsShow, isShow, rang } = props
    const [loading, setLoading] = useState(true)
    const [List, setList] = useState([

        {
            text: "Day",
            bo: true,
            warring: false
        },

        {
            text: "Week",
            bo: false,
            warring: false
        },
        {
            text: "Month",
            bo: false,
            warring: false
        }
    ]);
   
    const [activeTab, setActiveTab] = useState('Day')
    const [query, setQuery] = useState({
        "radarId": "1",
        "startTime": "",
        "endTime": "",
        "elderRoomRadarId": "1"
    })
    const [chatData, setchatData] = useState({})
    const bioInit = async () => {
        setchatData([])
        if (query.startTime == "") return
        setLoading(true)
        let query1 = { ...query }
        if (activeTab == "Day") {
            query1.endTime = ""
        }
        Promise.all([
            getBioVtalSummary({
                ...query1, ...Dailyreport
            }),
            getReportSummary()
        ])
            .then(res => {
                let data1 = res[0]?.data
                setchatData(data1)
                setTimeout(() => {
                    setLoading(false)
                }, 1000)
            })

    }
    const handleTimerClick = (row) => {
        setLoading(true)
        if (activeTab == "Day") {
            setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD') })
        } else {

            setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: row?.endvalue?.format('YYYY-MM-DD') })
        }
    }
    useEffect(() => {

        if (isShow) {
            bioInit()
        }
    }, [isShow, query])

    return (
        <> {props.isShow &&
            <div className='mode-back'>
                <div className='sleepModel'>

                    <ModelTitle title={"Bio-Vital Summary"} setIsShow={SetIsShow} />
                    <div className='flex flex-col items-center mb-[]'>

                        <div className='flex mt-[44px]'>

                            <TabMenus menus={List} activeTab={activeTab} setActiveTab={setActiveTab} setchatData={setchatData} />

                        </div>
                        <div className=' mt-[45px]  justify-center items-center'>
                            <TimeSelection name={activeTab} handleClick={handleTimerClick} />

                        </div>
                    </div>
                    <Spin spinning={loading}>
                        <BotvICom activeTab={activeTab} chatData={chatData}  query={query}  />
                    </Spin>
                    {/* <HistableCom query={query} setOpen2={false} /> */}
                </div>

            </div >
        }
        </>
    );
};

export default BioVitalSummary;
