import React, { useState,useEffect  } from 'react';
import ReactEcharts from 'echarts-for-react';
import 'echarts/lib/chart/bar';
import moon from '@/assets/icon/moon.svg'
function ActivWeekChart({charData}) {

    const [option, setOption] = useState({})

    const init = () => {
     let color=charData?.color
        setOption({
            title: {
                text: charData?.seriesdata?.xdata?.length === 0 ? "There is currently no data available" : '',
                show: true,
                left: "center",
                top: "center",
                textStyle: {
                    color: "#aaa",
                    fontSize: "20px",
                    fontWeight: 400,
                },
            },
            backgroundColor: 'rgb(247, 247, 255)',
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                    type: 'cross'
                }
            },
            grid: {
                top: "10%",
                left: "5%",
                bottom: "10%",
                right: "3%",

            },

            xAxis: {
                splitLine: { show: false },
                axisTick: { show: false },
                type: 'category',
                boundaryGap: false,
                // prettier-ignore
                data: charData?.seriesdata?.xdata
            },
            yAxis: {
                show: true,
                // axisLabel: { show: false },
                // splitLine: { show: false },
                axisLine: { show: true },
                type: 'value'
            },
            // graphic: [
            //     {
            //         type: 'image',
            //         id: 'mySVG',
            //         right: '5%', // 
            //         top: '12%',  // 
            //         z: 10, // 
            //         bounding: 'raw', // 
            //         style: {
            //             image: moon, // SVG  URL
            //             width: 19, //  SVG 
            //             height: 19, //  SVG 
            //             opacity: 1, // 
            //         }
            //     }
            // ],

            series: [
                {
                    name: 'Minutes:',
                    type: 'line',
                    smooth: true,
                    color:color,
                    // prettier-ignore
                    data: charData?.seriesdata?.values,
                    itemStyle: {
                        borderColor: color, // 
                        borderWidth: 2 // 
                    },
                    label: {

                        show: true, // 
                        position: 'top', // 'top' 
                        color: color,
                        formatter: function (params) {
                            // 
                            const dataArr =  charData?.seriesdata?.values;
                            // 
                            const maxValue = Math.max(...dataArr);
                            const minValue = Math.min(...dataArr);
                            // 
                            if (params.value === maxValue) {
                                return 'MAX: ' + params.value;
                            } else if (params.value === minValue) {
                                return 'MIN: ' + params.value;
                            } else {
                                return '';
                            }
                        }
                    },
                    symbol: 'circle',
                    symbolSize: 10,
                    // markArea: {
                    //     itemStyle: {
                    //         color: '#E6E6F6'

                    //     },
                    //     data: [
                    //         [
                    //             {
                    //                 name: '',
                    //                 xAxis: 'T'
                    //             },
                    //             {
                    //                 xAxis: 'S'
                    //             }
                    //         ],

                    //     ]
                    // }
                }
            ]
        })
       
    }
    useEffect(() => {
        init()
    }, [charData])

    return <ReactEcharts option={option} notMerge={true} />;
}

export default ActivWeekChart;