import React, { useEffect, useState } from 'react';
import { Spin, Table, Tag } from 'antd';
import dayjs from 'dayjs'
import ActivitiesReport from './ActivitiesReport.jsx';
import ActiveitiesModel from './ActiveitiesModel.jsx'
import { dailyactivity, getReportSummary } from '@/api/detailOfElderly'
import SleepHistorySummary from './SleepHistorySummary.jsx';
import WeeklyCom from './weeklyCom.jsx'
import ActiviteScom from './ActiviteScom'
import { calculateDuration } from '@/utils/comFunction'
import HistableCom from './HistableCom.jsx'
import { useCountStore } from '@/store/index';

const Botvial = (props) => {
    const Dailyreport = useCountStore(state => state.Dailyreport);
    const { activeTab, path } = props
    const [chartData, setChartData] = useState([])
    const [cards, setCards] = useState([])
    const [isShow2, SetIsShow2] = useState(false)
    const [isShow1, SetIsShow1] = useState(false)
    const [isShow, SetIsShow] = useState(false)
    const [isWeekShow, setIsWeekShow] = useState(false)
    const [loading, setLoading] = useState(true)

    const [query, setQuery] = useState({
        "endTime": "",
        "dateType": "day",
        "startTime":dayjs().format('YYYY-MM-DD'),
    })

   
    const [menus1, setMenus1] = useState([
        {
            text: "Sedentary",
            bo: true,
            warring: false
        },
        {
            text: "Bathroom Activity",
            bo: true,
            warring: false

        },
        {
            text: "Activity",
            bo: true,
            warring: false

        },
        {
            text: "Out of Bed",
            bo: true,
            warring: false

        },
        {
            text: "Night Activity",
            bo: true,
            warring: false

        },


    ])
    const init = () => {
        Promise.all([
            dailyactivity({
               ...query,...Dailyreport,
               "radarType": 0
            }),
            getReportSummary()])
            .then(res => {
                console.log(res)
                if(res && res[0]?.data) {
                    console.log('Response:', res);
                let { detailList, groupJSON } = res[0]?.data || {}
                let datalist = Object.values(detailList)
                datalist = datalist.map(e => {
                    return {
                        ...e,
                        minutesTimerCount: formatDuration(e.minutesCount),
                        valueStatu: e.valueStatu ? e.valueStatu : 0
                    }
                })

                let timerdatas = calculateDuration(Object.values(groupJSON).flat(2))
                console.log('timerdatas',timerdatas)
                 setChartData(timerdatas)
               
                setCards(datalist)
                }
                setLoading(false)
            })

    }
    const formatDuration = (minutes) => {
        const hours = Math.floor(minutes / 60);
        const remainingMinutes = minutes % 60;
        if (hours === 0) {
            return `${remainingMinutes}m`;
        } else {
            return `${hours}h ${remainingMinutes}m`;
        }
    }
    useEffect(() => {
        if (activeTab === 'Activities') {
            init()
        }

    }, [activeTab])
  


 

    return (
        <div>
            <Spin spinning={loading}>
                <ActiviteScom cards={cards} chartData={chartData} SetIsShow={SetIsShow} />
                <WeeklyCom setIsShow={SetIsShow1}  path={path} query={query} activeTab={activeTab}/>
                {/* <HistableCom query={{}} setOpen2={SetIsShow2} /> */}
                <SleepHistorySummary title="Activities History" isShow={isShow2} setIsShow={SetIsShow2} />
                <ActivitiesReport  formatDuration={formatDuration} calculateDuration={calculateDuration} isShow={isShow} SetIsShow={SetIsShow} />
                <ActiveitiesModel  
                SetIsShow={SetIsShow1} isShow={isShow1} />
                {/* <WeekSummary isShow={isWeekShow} SetIsShow={setIsWeekShow} /> */}
            </Spin>
        </div>
    );
};

export default Botvial;
