import React, { useState, useEffect } from 'react';
import { Modal } from 'antd';
import { elderDetail } from '@/api/detailOfElderly'

import Avatar from '@/Components/svg/Avatar'
import Bed from '@/Components/svg/bed'
import Timer from '@/Components/svg/Timer'
import { Progress, Input } from 'antd';
import { markDone } from '@/api/detailOfElderly'
import toast from "react-hot-toast";
import CustomToast from "@/Shared/Tosat/CustomToast";
import sidebarlogo from "@/assets/sidebarlogo.svg";

const { TextArea } = Input;
const OverviewModel = ({ setIsModalOpen, isModalOpen, config, path, setLoading }) => {
    const [item, setitem] = useState(config)
    const [params, setParams] = useState({
        comment:""
    })
    const showModal = () => {
        setIsModalOpen(true);
    };
    const handleOk = () => {
        setIsModalOpen(false);
    };
    const handleCancel = () => {
        setIsModalOpen(false);
    };
    const handleSubmit=()=>{
        markDone({
            activeId:config.id,
            type:1,
            comment:params.comment
        }).
        then(data=>{
            setLoading(true)
            toast.custom((t) => (
                <CustomToast
                  t={t}
                  text={"Operation successful"}
                  title={"Error"}
                />
              ));
            setIsModalOpen(false)
        
        })
    }
    // const getUser = () => {
    //     console.log("config",config)
    //     elderDetail({ activeId: config.id })
    //         .then(res => {

    //             let e = res.data
           
    //             setitem(res.data)
    //         })
    // }

    const curColor = (type) => {
        return type === 2 ? '#eb3d3d' : '#FAB515'
    }

    useEffect(() => {
        // if (isModalOpen) {
        //     getUser()
        // }
        setitem(config)
    }, [isModalOpen,config])
    return (
        <>

            <Modal
                footer={null}
                width={658}
                closeIcon={null}
                title=""
                open={isModalOpen}
                onOk={handleOk}
                onCancel={handleCancel}
                destroyOnClose={true}
            >
                <div className=' pr-[36px] pl-[36px] ' >
                    <div className='flex justify-center relative'>
                        <div className='pt-[50px] flex items-center'>
                            <div className='text-[20px] text-cblock font-semibold '>Mark Alert as Complete</div>
                            <div className='absolute right-[0] ' onClick={handleCancel}>
                                <svg xmlns="http://www.w3.org/2000/svg" width="29" height="28" viewBox="0 0 29 28" fill="none">
                                    <path d="M14.667 28C6.9473 28 0.666992 21.7197 0.666992 14C0.666992 6.2803 6.9473 0 14.667 0C22.3867 0 28.667 6.2803 28.667 14C28.667 21.7197 22.3867 28 14.667 28ZM14.667 1.99994C8.05023 1.99994 2.66693 7.38324 2.66693 14C2.66693 20.6168 8.05023 26.0001 14.667 26.0001C21.2838 26.0001 26.6671 20.6168 26.6671 14C26.6671 7.38324 21.2838 1.99994 14.667 1.99994Z" fill="black" />
                                    <path d="M19.667 19.9999C19.4111 19.9999 19.1552 19.9023 18.9599 19.7068L8.96003 9.70693C8.56931 9.31621 8.56931 8.68346 8.96003 8.29296C9.35053 7.90245 9.9835 7.90224 10.374 8.29296L20.3739 18.2929C20.7646 18.6836 20.7646 19.3163 20.3739 19.7068C20.1787 19.9023 19.9229 19.9999 19.667 19.9999Z" fill="black" />
                                    <path d="M9.66691 20C9.41099 20 9.15528 19.9024 8.96003 19.7069C8.56931 19.3164 8.56931 18.6834 8.96003 18.2929L18.9599 8.29304C19.3507 7.90232 19.9834 7.90232 20.3739 8.29304C20.7644 8.68354 20.7646 9.31651 20.3739 9.70701L10.374 19.7069C10.1787 19.9024 9.92283 20 9.66691 20Z" fill="black" />
                                </svg>
                            </div>
                        </div>

                    </div>
                    <div className='flex w-full justify-center mt-[30px]'>
                        <div
                            style={{ boxShadow: ' 0px 0px 50px -10px rgba(0, 0, 0, 0.15)', backgroundColor: curColor(item?.priority)  }}
                            className={` h-[200px] w-[200px]
                                rounded-[20px]
                                 pl-[20px] 
                                 pr-[20px] text-OnButtonNormal  
                                 rounded-[20px] 
                                  cursor-pointer  
                                  flex 
                                  flex-col
                                  justify-between
                                  pt-[20px]
                                  pb-[20px]
                                  items-center mr-[20px]  bg-onBackWarring } `}>
                            <div className='flex  items-center '>
                                <div
                                    style={{ backgroundColor: " #fff", borderRadius: "50%" }}
                                    className={`
                                  
                                           flex justify-center items-center
                                         rounded-[100px]
                                          w-[36px] 
                                          h-[36px]
                                          ${item?.priority == 0 ? 'bg-white' : item?.priority == 1 ? ' bg-Warning' : "bg-onBackWarring"}
                                          `}>
                                    {/* <Warning color="#FA4B5E" /> */}
                                    <img src={item?.iconUrl || sidebarlogo} className='w-full h-full' style={{ borderRadius: "50%" }}/>
                                </div>
                                <div

                                    className={` line-1  card-name
                                    w-[115px]
                                    ml-[10px] font-cblock text-[#fff] font-semibold leading-normal
                                        }
                                        `}>
                                    {item?.alertName}
                                </div>
                            </div>
                            {/*  */}
                            <div className='w-full   mt-[20px] '>

                                <div className='w-full flex  items-center '>
                                    <Avatar color="#fff" />
                                    <div style={{ color: "#fff" }} className='ml-[5px] text-cblock text-[12px] font-semibold'>{item?.lastName}</div>
                                </div>
                                <div className='w-full flex  items-center mt-[5px] '>
                                    <Bed color="#fff" />
                                    <div style={{ color: "#fff" }} className='ml-[5px] text-cblock text-[12px] '>Room {item?.roomNumber}</div>
                                </div>
                                <div className='w-full flex  items-center  mt-[5px] '>
                                    <Timer color="#fff" />
                                    <div style={{ color: "#fff" }} className='ml-[5px] text-cblock text-[12px]'>{item?.shortTime} {item?.displayYearTime}</div>
                                </div>
                            </div>
                            <div className=' w-[100px] press'>
                                <Progress
                                    showInfo={false} strokeColor={"#fff"} trailColor={"rgba(255,255,255,.3)"} percent={item?.calculatePercentage} />
                            </div>
                        </div>
                    </div>

                </div>
                <div className='mt-[33px] w-full h-[1px] bg-[#EBEBEB] '  >

                </div>
                <div className='w-full pr-[36px] pl-[36px] h-[111px] mt-[14px]'>
                    <TextArea rows={4} placeholder="Type a Comment here" maxLength={60} onChange={(e)=>{
                       setParams({...params,comment:e.target.value})
                    }} />
                </div>
                <div className='flex justify-center pb-[28px] mt-[10px]'>
                    <div onClick={()=>handleSubmit()} className=' font-semibold ml-[14px] font-cblock rounded-[20px] h-[40px] pl-[11px] pr-[11px] flex items-center w-[138px]   ' style={{ border: "1px solid #333951",cursor:'pointer' }}>Mark as Complet</div>
                </div>
            </Modal>
        </>
    );
};
export default OverviewModel;