
import React, { useEffect, useRef, useState } from 'react';
import './SleepScore.css';
import TabMenus from '@/Components/TabMenus/index'
import TimeSelection from '@/Components/TimeSelection/index'
import dayjs from 'dayjs'
import { formatDuration } from '@/utils/seenYouUtil'
import { sleepScore, getReportSummary } from '@/api/detailOfElderly'
import HisTable from './assembly/HisTable.jsx'
import BotLineChar from './assembly/BotLineChar.jsx'
import SleepScoreCard from './SleepScoreCard.jsx'
import totalsleeptime2 from '@/assets/icon/totalsleeptime2.svg'
import HistableCom from './HistableCom.jsx'
import { useCountStore } from '@/store/index';
import ModelTitle from '@/Components/ModelTitle/index'

const SleepScore = (props) => {
    const Dailyreport = useCountStore(state => state.Dailyreport);
    const { isShow, setIsShow } = props
    const [List, setList] = useState([

        {
            text: "Day",
            bo: true,
            warring: false
        },

        {
            text: "Week",
            bo: false,
            warring: false
        },
        {
            text: "Month",
            bo: false,
            warring: false
        }
    ]);

    const [activeTab, setActiveTab] = useState('Day')
    const [card, setCard] = useState([])
    const [botoomList, setBotoomList] = useState([])
    const [charData, setCharData] = useState({
        xdata: []
    })
    const [data, setData] = useState([])
    const [query, setQuery] = useState({
       
        startTime:dayjs().format('YYYY-MM-DD'),
        "endTime": ""
    })
    const calculateAverage = (numbers) => {
        if (numbers.length === 0) return 0;

        const sum = numbers.reduce((accumulator, current) => accumulator + current, 0);
        const average = sum / numbers.length;

        return average;
    }

    const formatSleepChartTime = (minutes) => {
        if(typeof minutes !== 'number') return minutes
        const hours = Math.floor(minutes / 60);
        const remainingMinutes = minutes % 60;
        if (hours === 0) {
            return `0.${remainingMinutes}`;
        } else {
            return `${hours}.${remainingMinutes}`;
        }
    }

    function convertMinutesToHours(minutes) {
        const hours = Math.floor(minutes / 60); // 
        const remainingMinutes = minutes % 60; // 
        const decimalHours = remainingMinutes; // 
        return Number(hours+'.'+decimalHours)
    }
    
    const bioInit = async () => {
        setBotoomList([])
        setCharData({
            xdata:[], values:[], average: 0
        })
        if (query.startTime == "") return
        let query1 = { ...query }
        if (activeTab == "Day") {
            query1.endTime = ""
        }
        Promise.all([
            sleepScore({
                ...query,
                ...Dailyreport
            }),
            getReportSummary()
        ])
            .then(res => {
                let data = res[0]?.data

                let dataInfo = data?.sleepScoreDailyVO?.detailInfo || data?.sleepScoreWeekVO?.topInfo || data?.sleepScoreMonthVO?.topInfo
                let sleepTime = data?.sleepScoreDailyVO?.detailInfo?.sleepTime || data?.sleepScoreWeekVO?.sleepTime || data?.sleepScoreMonthVO?.sleepTime
                let botoomList = data?.sleepScoreDailyVO?.botoomList || data?.sleepScoreWeekVO?.botoomList || data?.sleepScoreMonthVO?.botoomList
                let arr = []
                console.log('botoomList', botoomList)

                if (botoomList) {

                    let xdata = Object.keys(sleepTime)
                    let values = Object.values(sleepTime).map //(e => +(e / 60).toFixed(1))
                    (e => {
                        const showTime = convertMinutesToHours(e)
                        console.log(showTime)
                        return showTime
                    })
                    // let average = calculateAverage([...values]) //
                    let average = botoomList[2].numberStr.replace('h','.').replace('min','')
                    console.log(average)
                    if (xdata.length == 1) {
                        xdata = [0, xdata[0]]
                        values = [0, values[0]]
                    }
                    setCharData({
                        xdata, values, average
                    })
                    console.log(xdata)
                    console.log(values)
                    botoomList = botoomList.map((item, key) => {
                        item.reportTypeName = item.maxType + ' Sleep Time ' + (item.specTime || '')
                        item.minutesCount = item.number
                        item.trend = item.targetValue
                        item.src = totalsleeptime2
                        return item
                    })

                    setCharData({
                        xdata, values, average
                    })
                    setBotoomList(botoomList)
                }

                setCard(data)
            })

    }

    useEffect(() => {
        if (isShow) {
            bioInit()
        }
    }, [isShow,query])

    const handleTimerClick = (row) => {

        if (activeTab == "Day") {
            setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: "" })
        } else {

            setQuery({ ...query, startTime: row.value.format('YYYY-MM-DD'), endTime: !row.endvalue ? "" : row.endvalue.format('YYYY-MM-DD') })
        }
    }

    return (
        <>
            {
                isShow &&
                <div className='mode-back'>
                    <div className='sleepModel'>
                        <ModelTitle title={"Sleep Score"} setIsShow={setIsShow} />
                        <div className='flex flex-col items-center'>
                            <div className='flex mt-[44px]'>
                                <TabMenus menus={List} activeTab={activeTab} setActiveTab={setActiveTab} />
                            </div>
                            <div className='flex mt-[45px] w-[256px] justify-between items-center'>
                                <TimeSelection name={activeTab} handleClick={handleTimerClick} />
                            </div>
                        </div>

                        <SleepScoreCard  card={card} isShow={false} setOpen={false} activeTab={activeTab}/>
                        {
                            activeTab !== "Day" && <>
                                <div className='mt-[50px] pb-[10px] flex justify-between'>
                                    <div className='text-[20px] text-cblock font-semibold'>Sleeping Time</div>
                                </div>
                                <BotLineChar charData={charData} activeTab={activeTab}/>
                                <div>
                                    <HisTable data={botoomList} hideColumns={['target','trend','type']} />
                                </div>
                            </>
                        }
                        {/* <HistableCom query={query} setOpen2={false} /> */}
                    </div>

                </div>
            }

        </>
    );
};

export default SleepScore;
