import React, { useState, useEffect } from 'react';
import TabMenus from '@/Components/TabMenus'
import WeeklyChar from './WeeklyChar'
import { Spin, Table, Tag } from 'antd';
import HisCards from './assembly/HisCards'
import HisTable from './assembly/HisTable'
import BotLineChar from './BotLineChar'
import { setStatusFormate } from '@/utils/comFunction'
import yellavgRate from '@/assets/icon/yellavgRate.svg'
import avgheart from '@/assets/icon/avgheart.svg'
import dayjs from 'dayjs'
import SummaryBtn from '@/Components/SummaryBtn'
export default function BotvITal({ SetIsShow, chatData }) {
    const [card, setCard] = useState([])
    const [charData, setCharData] = useState(null)
    const [data, setData] = useState([])
    const [loading, setLoading] = useState(true)
    const init = () => {
        setLoading(true)
        setData([])
        setCard([])
        setCharData(null)
        let data1 = chatData
        console.log("data1",data1)
        if(!data1?.breathAvgInfo) return 
        // let groupJSON = data1?.groupJSON
        let groupJSON = JSON.stringify(data1.groupJSON) === "{}" ? {["6"]:[],["7"]:[]} : data1.groupJSON
        if(JSON.stringify(data1.groupJSON) === "{}") return
        setCard([
            {
                valueStatu: setStatusFormate(data1.breathAvgInfo.trendStatu),
                priority: data1.breathAvgInfo.priority,
                minutesCount: data1.breathAvgInfo.minutesCount,
                reportTypeName: "Avg. Respiratory Rate",

            },
            {
                valueStatu: setStatusFormate(data1.heartAvgInfo.trendStatu),
                priority: data1.heartAvgInfo.priority,
                minutesCount: data1.heartAvgInfo.minutesCount,
                reportTypeName: "Avg. Heart Rate",

            }
        ])

        setCharData([
            {
                array: groupJSON["6"]?.map(e => {
                    {
                        return e.value
                    }
                }),
                name: `Avg. ${data1.heartAvgInfo.minutesCount}  bpm`,
                value: data1.heartAvgInfo.minutesCount,
                info: data1.heartAvgInfo.extendInfo,
                timer: groupJSON["6"]?.map(e => {
                    {
                        return dayjs(e.reportStartTime).format('HH:mm')
                    }
                }),
                color: "#FA4B5E",
                src: avgheart
            },
            {
                array: groupJSON["7"]?.map(e => {
                    {
                        return e.value
                    }
                }),
                name: `Avg. ${data1.breathAvgInfo.minutesCount} bpm`,
                value: data1.breathAvgInfo.minutesCount,
                info: data1.breathAvgInfo.extendInfo,
                timer: groupJSON["6"]?.map(e => {
                    {
                        return dayjs(e.reportStartTime).format('HH:mm')
                    }
                }),
                color: "#FAB515",
                src: yellavgRate
            },
        ])

        let herfInfo = data1?.heartAvgInfo
        let herftarget
        if (herfInfo?.targetMax && herfInfo?.targetMin) {
            herftarget = `${herfInfo.targetMin}-${herfInfo.targetMax}`
        } else if (herfInfo?.targetMax) {
            herftarget == herfInfo?.targetMax
        } else {
            herftarget == herfInfo?.targetMin
        }

        let breathInfo = data1?.breathAvgInfo
        let breathtarget
        if (breathInfo?.targetMax && breathInfo?.targetMin) {

            breathtarget = `${breathInfo.targetMin}-${breathInfo.targetMax}`
        } else if (breathInfo.targetMax) {

            breathtarget = breathInfo.targetMax
        } else if (breathInfo.targetMin) {

            breathtarget = breathInfo.targetMin
        }

        setData([
            {
                valueStatu: data1?.heartAvgInfo.valueStatu,
                reportTypeName: "Average Heart Rate",
                targetValue: herftarget,
                minutesCount: data1?.heartAvgInfo?.minutesCount,
                minutesCountStr: data1?.heartAvgInfo?.minutesCountStr,
                trend: data1?.heartAvgInfo.trend,
                priority: 0, //data1.heartAvgInfo.priority,
                trendStatu: data1?.heartAvgInfo?.trendStatu,
                src: avgheart,
                target: data1?.heartAvgInfo.target,
                unit: data1?.heartAvgInfo.unit
            },
            {
                valueStatu: data1?.breathAvgInfo.valueStatu,
                reportTypeName: "Average Respiratory rate",
                targetValue: breathtarget,
                minutesCount: data1?.breathAvgInfo.minutesCount,
                minutesCountStr: data1?.breathAvgInfo.minutesCountStr,
                trend: data1?.breathAvgInfo.trend,
                priority: 0, //data1.breathAvgInfo.priority,
                trendStatu: data1?.breathAvgInfo?.trendStatu,
                src: yellavgRate,
                target: data1?.breathAvgInfo.target,
                unit: data1?.breathAvgInfo.unit
            }
        ])
       setTimeout(() => {
        setLoading(false)
       }, 1000);
    }
    useEffect(() => {
        init()
    }, [chatData])
    return (
        <>
            <div className='pt-[10px] pb-[10px] flex justify-between'>
                <div className='text-[20px] text-cblock font-semibold'>Bio-Vital Summary</div>
                {SetIsShow && <SummaryBtn onClick={() => SetIsShow(true)} />}
            </div>
            <div className='pt-[10px] flex '>
                <HisCards data={card} />
            </div>
            <BotLineChar charData={charData} />
            <div className='min-h-[300px] bg-lightwhite'>
                <HisTable data={data} bo={false} />
            </div>
        </>
    )
}