import React, { useEffect, useState } from 'react';
import ReactEcharts from 'echarts-for-react';
import 'echarts/lib/chart/bar';
import dayjs from 'dayjs'
import moon from '@/assets/icon/moon.svg'


function MyChart({ activeTab1, charData }) {

  const [option, setOption] = useState({})
  const init = () => {


    if (charData?.seriesdata?.length > 0) {

      let data = option.series
      setOption({
        backgroundColor: 'rgba(0,0,0,0 )',
        grid: {
          left: '0',
          right: '4%',
          bottom: '3%',
          top: '10%',
          containLabel: true
        },
        xAxis: {
          type: 'time',
          min: '2022-01-01 00:00', // 
          max: '2022-01-01 23:59', // 
          axisLabel: {
            formatter: function (value) {
              //  dayjs 
              return dayjs(value).format('HH:mm');
            }
          },
          splitLine: {
            show: false // 
          },

          axisTick: { show: false },

          boundaryGap: false // Y
        },
        yAxis: {
          show: false,
          name: 'bpm', // 
          nameLocation: 'end', // 'end' 
          nameTextStyle: { // 
            color: '#333',
            fontSize: 12,
            padding: [0, 0, 10, -10] // 
          },
          splitLine: { show: false },
          axisLine: { show: true },
          type: 'value',
          axisLabel: {
            color: "#000",
          },
          max: "900"
        },
        series: [
          {
            data: charData.seriesdata,
            type: 'line',
            color: charData.color,
            areaStyle: {},

            showSymbol: false, // 
            markArea: {
              itemStyle: {
                color: '#E6E6F6'
              },
              data: [
                [
                  {
                    name: '',
                    xAxis: '2022-01-01 18:00'
                  },
                  {
                    xAxis: '2022-01-01 23:59'
                  }
                ],

              ]
            }
          }
        ],
        graphic: [
          {
            type: 'image',
            id: 'mySVG',
            right: '5%', // 
            top: '12%',  // 
            z: 10, // 
            bounding: 'raw', // 
            style: {
              image: moon, // SVG  URL
              width: 19, //  SVG 
              height: 19, //  SVG 
              opacity: 1, // 
            }
          }
        ]
      })

    } else {
      setOption({
        backgroundColor: 'rgba(0,0,0,0 )',
        title: {

          text: "There is currently no data available",
          show: true,
          left: "center",
          top: "center",
          textStyle: {
            color: "#000",
            fontSize: "20px",
            fontWeight: 400,
          },
        },
      })
    }

  }


  useEffect(() => {
    init()
  }, [charData])



  return <ReactEcharts option={option} notMerge={true} />;
}

export default MyChart;